import React from 'react';
import styled from "@emotion/styled";
import data from "./postData";
import MediaQuery from 'react-responsive';
import PostItems, { Items, TextArea } from "./PostItems";

const Container = styled.div`
  width: 80%;
  max-width: 1124px;
  margin: auto;
  padding: 150px 0px;
  @media screen and (max-width: 767px) {
    padding: 80px 0px;
  }
`;
const SubTitle = styled.p`
  margin-top: 20px;
  font-weight: 500;
  font-size: 1.375rem;
  line-height: 2.75rem;
  text-align: center;
  letter-spacing: 0.1em;
  color: #36563C;
  @media screen and (max-width: 767px) {
    font-size: 1.125rem;
    line-height: 2rem;
  }
`;

const Title = styled.h2`
  font-weight: bold;
  font-size: 2.1875rem;
  line-height: 3.75rem;
  text-align: center;
  letter-spacing: 0.1em;
  color: #36563C;
  @media screen and (max-width: 767px) {
    font-size: 1.5rem;
    line-height: 2.5rem;
  }
`;

const Wall = styled.div`
  width: 100%;
  margin-top: 120px;
  @media screen and (min-width: 1024px) {
    ${Items} {
      &:nth-of-type(odd) {
        ${TextArea} {
          padding-left: 50px;
        }
      }
      &:nth-of-type(even) {
        flex-direction: row-reverse;
        ${TextArea} {
          padding-right: 50px;
        }
      }
    }
  }
  @media screen and (max-width: 767px) {
    margin-top: 80px;
  }
`;




const PostWall = () => {
  return (
    <Container>
      <Title>開始吃蔬食的五大理由</Title>
      <SubTitle>
        每一位在餐桌前的你
        <MediaQuery minWidth={767}>，</MediaQuery>
        <MediaQuery maxWidth={767}><br /></MediaQuery>
        其實都擁有改變世界的機會。
      </SubTitle>
      <Wall>
        {data.map((items, key) => {
          return <PostItems {...items} key={key} />
        })}
        {/* <PostItems />
        <PostItems /> */}
      </Wall>
    </Container>
  )
};

export default PostWall;