import React from "react";
import { css } from "@emotion/css";
import styled from "@emotion/styled/macro";
import mapStrToBr from "../../utility/mapStrToBr";
import convertArrayIntoSingleFragment from "../../utility/convertArrayIntoSingleFragment";
import more from "../../images/more2.svg";
import arrow1 from "../../images/arrow1.svg";
import arrow2 from "../../images/arrow2.svg";
import useWindowResize from "../hooks/UseWindowResize";
import { Link } from "react-router-dom";
import ReactGA from "react-ga";

const Item = styled.div`
  position: relative;
  width: 100%;
  display: flex;
  align-items: flex-start;
  flex-wrap: wrap;
  position: relative;
  /* height: 750px; */
`;

const MaskBox = styled.div`
  position: relative;
  width: 100%;
  mask-size: contain;
  mask-repeat: no-repeat;
  background-image: url(${(props) => props.bg});
  background-size: cover;
  background-position: center;
  background-color: ${(props) => props.full && "#6C9373"};
  &::before {
    content: "";
    display: block;
  }
  img {
    width: 100%;
  }
`;

const Line = styled.div`
  width: 100%;
  height: 1px;
  background-color: #637c59;
  margin: 30px 0px;
`;

const TextBox = styled.div`
  width: 100%;
  h4 {
    margin-top: 30px;
    font-weight: 700;
    font-size: 1.5625rem;
    line-height: 2.625rem;
    letter-spacing: 0.1em;
    color: ${(props) => (!props.comingSoon ? "#36563C" : "#B4B4B4")};
    @media screen and (max-width: 767px) {
      font-size: 1.125rem;
      line-height: 1.875rem;
    }
  }
  p {
    margin-top: 30px;
    font-weight: 400;
    font-size: 1rem;
    letter-spacing: 0.1rem;
    line-height: 2rem;
    color: ${(props) => (!props.comingSoon ? "#000" : "#B4B4B4")};
    text-align: justify;
    @media screen and (max-width: 767px) {
      font-size: 0.875rem;
      line-height: 1.5rem;
    }
  }
  ${Line} {
    background-color: ${(props) => (!props.comingSoon ? "#637C59" : "#B4B4B4")};
  }
`;

const ArrowLeft = css`
  position: absolute;
  right: -12px;
  transform: translateY(-50%);
  top: 38%;
  z-index: 1;
  @media screen and (max-width: 1024px) {
    top: 35%;
  }
  @media screen and (max-width: 768px) and (orientation: portrait) {
    top: 27%;
    width: 70px;
  }
`;
const ArrowRight = css`
  position: absolute;
  left: -10px;
  transform: translateY(-50%);
  top: 56%;
  z-index: 1;
  @media screen and (max-width: 1024px) {
    top: 46%;
  }
  @media screen and (max-width: 768px) and (orientation: portrait) {
    top: 34%;
    width: 70px;
    left: -7px;
  }
`;

const ArrowMobile = css`
  position: absolute;
  left: -8px;
  transform: translateY(-50%);
  top: 38%;
  z-index: 1;
  width: 70px;
`;

const MoreBtn = styled.a`
  text-align: right;
  display: inherit;
  img {
    width: 82px;
  }
`;

const CustomBtn = css`
  text-align: right;
  display: inherit;
  img {
    width: 82px;
  }
`;

const Note = styled.p`
  font-weight: 500;
  font-size: 2.1875rem;
  letter-spacing: 0.1em;
  color: rgba(255, 255, 255, 0.95);
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  @media screen and (max-width: 767px) {
    font-size: 1.25rem;
  }
`;
const Warning = styled.span`
  display: block;
  margin-top: 30px;
  font-weight: 400;
  font-size: 1rem;
  letter-spacing: 0.1rem;
  line-height: 2rem;
  color: red;
  text-align: justify;
  @media screen and (max-width: 767px) {
    font-size: 0.875rem;
    line-height: 1.5rem;
  }
`;

const TreePost = ({
  img,
  title,
  text,
  link,
  index,
  comingSoon,
  customPage = false,
  warning,
}) => {
  const mobile = useWindowResize();
  return (
    <Item>
      <MaskBox bg={img} full={comingSoon && true}>
        {comingSoon && <Note>敬請期待</Note>}
      </MaskBox>
      {!mobile ? (
        <img
          src={index % 2 === 1 ? arrow1 : arrow2}
          className={index % 2 === 1 ? ArrowLeft : ArrowRight}
          alt="img"
        />
      ) : (
        <img src={arrow2} className={ArrowMobile} alt="img" />
      )}

      <TextBox comingSoon={comingSoon}>
        <h4>{title}</h4>
        <p>{convertArrayIntoSingleFragment(mapStrToBr(text))}</p>
        {warning && <Warning>{warning}</Warning>}
        <Line />
        {!comingSoon &&
          (customPage ? (
            <Link
              className={CustomBtn}
              to={link}
              onClick={() => {
                ReactGA.event({
                  category: "HomePage Btn",
                  action: "Click",
                  label: `HomePage ${title}-了解更多`,
                });
              }}
            >
              <img src={more} alt="more img" />
            </Link>
          ) : (
            <MoreBtn href="" rel="noopener noreferrer">
              <img src={more} alt="more img" />
            </MoreBtn>
          ))}
      </TextBox>
    </Item>
  );
};

export default TreePost;
export { Item, MaskBox, TextBox };
