import React from 'react';
import styled from "@emotion/styled/macro";
import ReactGA from "react-ga";

const Items = styled.div`
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
  &:not(:last-child) {
    margin-bottom: 200px;
    @media screen and (max-width: 767px) {
      margin-bottom: 150px;
    }
  }
`;

const Left = styled.div`
  width: 45%;
  height: 100%;
  /* background-image: url(${props => props.img});
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  &::before {
    content: '';
    padding-bottom: 75.72%;
    display: block;
  } */
  img {
    width: 100%;
  }
  @media screen and (max-width: 768px) {
    width: 50%;
    margin: 0 auto 40px;
  }
  @media screen and (max-width: 767px) {
    width: 75%;
    margin-bottom: 40px;
  }
`;

const Right = styled.div`
  width: 50%;
  @media screen and (max-width: 768px) {
    width: 100%;
  }
`;

const TextArea = styled.div`
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
`;

const Title = styled.div`
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 40px;
  h3 {
    font-weight: 500;
    font-size: 1.875rem;
    line-height: 3.125rem;
    letter-spacing: 0.1em;
    color: #36563C;
  }
  img {
    width: 60px;
    max-width: 100%
  }
  @media screen and (max-width: 1024px) {
    margin-bottom: 20px;
    h3 {
      font-size: 1.25rem;
    }
    img {
      width: 40px;
    }
  }
  @media screen and (max-width: 768px) {}
`;

const Text = styled.p`
  font-size: 1.125rem;
  line-height: 2.25rem;
  text-align: justify;
  color: #000000;
  margin-bottom: 50px;
  @media screen and (max-width: 1024px) {
    font-size: 1rem;
    line-height: 1.875rem;
  }
`;

const Line = styled.div`
  width: 100%;
  height: 1px;
  background-color: #637C59;
  margin-top: auto;
`;

const More = styled.a`
  margin-top: 20px;
  font-weight: 500;
  font-size: 1.125rem;
  line-height: 1.5625rem;
  text-align: right;
  color: #36563C;
  align-self: flex-end;
  position: relative;
  padding-right: 20px;
  &::before {
    content: '';
    width: 15px;
    height: 2px;
    background-color: #36563C;
    position: absolute;
    top: 50%;
    right: 0px;
  }
  &::after {
    content: '';
    width: 2px;
    height: 15px;
    background-color: #36563C;
    position: absolute;
    top: 50%;
    right: 0px;
    transform: translateY(-40%);
    right: 7px;
  }
  @media screen and (max-width: 1024px) {
    font-size: 1rem;
  }
`;


const PostItems = ({ img, title, text, link, className }) => {
  return (
    <Items>
      <Left >
        <img src={img} className={className} alt="" />
      </Left>
      <Right>
        <TextArea>
          <Title>
            <h3>{title}</h3>
          </Title>
          <Text>{text}</Text>
          <Line />
          <More href={link} rel="noopener noreferrer" onClick={() => {
            ReactGA.event({
              category: "Reason Btn",
              action: "Click",
              label: `Reason ${title}-延伸閱讀`
            });
          }}>延伸閱讀</More>
        </TextArea>
      </Right>
    </Items>
  )
};

export default PostItems;
export { Items, TextArea }