import React, { memo } from "react";
import styled from "@emotion/styled";
import FlowContainer from "./FlowContainer";
import { challengeData } from "./constants ";

const ChallengeContainer = styled.div`
  width: 100%;
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  margin-top: 100px;
  @media screen and (max-width: 1024px) {
    justify-content: center;
  }
`;
const ChallengeItems = styled.div`
  width: 30%;
  overflow: hidden;
  border-radius: 20px;
  display: flex;
  flex-direction: column;
  @media screen and (max-width: 1024px) {
    width: 55%;
    &:not(:last-of-type) {
      margin-bottom: 60px;
    }
  }
  @media screen and (max-width: 1024px) {
    width: 100%;
  }
`;
const ChallengeTop = styled.div`
  background-color: ${(props) => props.theme};
  padding: 30px 15px;
  color: #fff;
  p {
    font-size: 25px;
    line-height: 0.1em;
    text-align: center;
    font-weight: bold;
    line-height: 32px;
    letter-spacing: 0.1em;
  }
  span {
    display: block;
    text-align: center;
    font-size: 20px;
    letter-spacing: 0.1em;
    margin-top: 10px;
  }
  @media screen and (max-width: 1336px) {
    p {
      font-size: 22px;
    }
    span {
      font-size: 18px;
    }
  }
`;

const ChallengeInfo = styled.div`
  position: relative;
  width: 100%;
  height: 100%;
  padding: 35px;
  display: flex;
  flex-direction: column;
  background-color: #f3f6e7;
  > p {
    font-weight: 500;
    font-size: 20px;
    color: #000;
    letter-spacing: 0.1em;
    margin-bottom: 25px;
  }
  ul {
    li {
      display: flex;
      align-content: center;
      &:not(:last-of-type) {
        margin-bottom: 10px;
      }
    }
    img {
      display: block;
      width: 25px;
      margin-right: 10px;
    }
    p {
      font-weight: 500;
      font-size: 18px;
      letter-spacing: 0.05em;
      color: #000000;
    }
  }
  @media screen and (max-width: 1336px) {
    > p {
      font-size: 18px;
    }
    ul {
      p {
        font-size: 16px;
      }
    }
  }
`;

const HashTag = styled.div`
  width: 100%;
  min-height: 100px;
  p {
    font-weight: 500;
    font-size: 16px;
    line-height: 32px;
    letter-spacing: 0.05em;
    color: #36563c;
    margin-top: 30px;
    text-align: justify;
    span {
      display: inline-block;
      vertical-align: middle;
      margin: 0 2px;
      width: 30px;
      height: 30px;
      background-size: cover;
      background-position: center;
    }
    @media screen and (max-width: 767px) {
      line-height: 32px;
    }
  }
`;

const Btn = styled.div`
  cursor: pointer;
  width: 90%;
  max-width: 260px;
  margin: auto auto 0;
  border-radius: 40px;
  background-color: ${(props) => props.theme};
  padding: 16px 10px;
  text-align: center;
  p {
    position: relative;
    display: inline-block;
    color: #fff;
    font-size: 24px;
    letter-spacing: 0.1em;
    font-weight: bold;
    &::after {
      content: "!";
      display: inline-block;
      transform: rotate(15deg);
      font-size: 26px;
      position: absolute;
      top: -2px;
      right: -18px;
    }
  }
  @media screen and (max-width: 1336px) {
    padding: 10px;
    p {
      font-size: 20px;
      &::after {
        top: -6px;
      }
    }
  }
`;

const Challenge = ({ handleSelected }) => {
  return (
    <FlowContainer title={"馬上報名挑戰"}>
      <ChallengeContainer>
        {challengeData.map(({ color, title, subTitle, note, text, icon }) => (
          <ChallengeItems key={title}>
            <ChallengeTop theme={color}>
              <p>
                {title}
                <br />
                {subTitle}
              </p>
              <span>{note}</span>
            </ChallengeTop>
            <ChallengeInfo>
              <p>報名後 - 12/31 期間：</p>
              <ul>
                {text.map((i) => (
                  <li key={i}>
                    <img src={icon} alt="hand" />
                    <p>{i}</p>
                  </li>
                ))}
              </ul>
              <HashTag>
                <p>
                  歡迎公開以貼文形式分享無肉照片至FB/IG並標註
                  <br />
                  #1125全體素立
                  <br />
                  #MeatlessDayRocksOn
                  <br />
                  <br />
                </p>
              </HashTag>
              <Btn
                theme={color}
                onClick={() => {
                  handleSelected((prev) => ({
                    ...prev,
                    challengeSelected: title,
                  }));
                }}
              >
                <p>我要報名</p>
              </Btn>
            </ChallengeInfo>
          </ChallengeItems>
        ))}
      </ChallengeContainer>
    </FlowContainer>
  );
};

export default memo(Challenge);
