import React from "react";
import styled from "@emotion/styled";

const Container = styled.div`
  width: 100%;
  background-color: #fff;
  padding: 60px 20px;
  position: relative;
  &:first-of-type {
    border-top-left-radius: 10px;
    border-top-right-radius: 10px;
  }
  &:last-of-type {
    border-bottom-left-radius: 10px;
    border-bottom-right-radius: 10px;
  }
  &:not(:last-of-type) {
    &::after {
      content: "";
      width: calc(100% - 40px);
      height: 3px;
      position: absolute;
      bottom: 0px;
      left: 20px;
      border-bottom: 3px dashed #a9ce53;
    }
  }
  @media screen and (max-width: 767px) {
    /* padding: 60px 0px; */
  }
`;

const Title = styled.h3`
  text-align: center;
  letter-spacing: 0.1em;
  font-size: 35px;
  color: #231916;
  span {
    display: inline-block;
    &:first-of-type {
      margin-right: 15px;
    }
    &:last-of-type {
      margin-left: 15px;
    }
  }
  @media screen and (max-width: 767px) {
    font-size: 28px;
  }
`;

const FlowContainer = ({ id = null, title, children }) => {
  return (
    <Container id={id}>
      {title && (
        <Title>
          <span>&#92;</span>
          {title}
          <span>&#47;</span>
        </Title>
      )}

      {children}
    </Container>
  );
};

export default FlowContainer;
