import React from "react";
import styled from "@emotion/styled";
import { Link } from "react-router-dom";
import { NavHashLink as NavLink } from "react-router-hash-link";
import { withRouter } from "react-router-dom";
import lineBtn from "../images/lineBtn.svg";
import ReactGA from "react-ga";
// import Routes from "../Routes";

const Ul = styled.ul`
  display: flex;
  align-items: center;
`;
const LineBtn = styled.img`
  width: auto;
  height: 40px;
`;

const Li = styled.li`
  font-weight: 400;
  font-size: 1.125rem;
  letter-spacing: 0.1rem;
  /* margin-right: 20px; */
  > a,
  p {
    cursor: pointer;
    color: #fff;
    transition: all 0.2s;
    /* @media screen and (max-width: 1024px) {
      
    } */
  }
  @media screen and (min-width: 1024px) {
    padding: 0 1rem;
    &:nth-of-type(-n + 3) {
      position: relative;
      /* border-right: 2px solid #637C59; */
      &::after {
        content: "";
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
        right: 0;
        width: 2px;
        height: 18px;
        background-color: #637c59;
      }
    }
    &:last-child {
      padding-right: 0;
    }
  }
`;

const Menu = ({ handleClose }) => {
  const scrollOffset = (el) => {
    setTimeout(() => {
      let depth =
        el.id === "why" ? el.offsetHeight - el.children[0].offsetHeight : 0;
      window.scrollTo({
        top: el.offsetTop - 80 + depth,
        behavior: "smooth",
      });
    }, 500);
  };
  const handleGa = (label) => {
    ReactGA.event({
      category: "Menu",
      action: "Click",
      label: label,
    });
  };
  return (
    <Ul>
      <Li>
        <Link
          to="/register"
          onClick={() => {
            handleClose();
            handleGa("全體素立");
          }}
        >
          全體素立
        </Link>
      </Li>
      <Li>
        <NavLink
          smooth
          to="/#why"
          scroll={(el) => {
            scrollOffset(el);
          }}
          onClick={() => {
            handleClose();
            handleGa("為何減碳");
          }}
        >
          為何減碳
        </NavLink>
      </Li>
      <Li>
        <NavLink
          smooth
          to="/#life"
          scroll={(el) => {
            scrollOffset(el);
          }}
          onClick={() => {
            handleClose();
            handleGa("減碳生活");
          }}
        >
          減碳生活
        </NavLink>
      </Li>
      <Li>
        <NavLink
          smooth
          to="/#recommend"
          scroll={(el) => {
            scrollOffset(el);
          }}
          onClick={() => {
            handleClose();
            handleGa("蔬食餐廳推薦");
          }}
        >
          蔬食餐廳推薦
        </NavLink>
      </Li>
      <Li>
        <NavLink
          smooth
          to="/#extend"
          scroll={(el) => {
            scrollOffset(el);
          }}
          onClick={() => {
            handleClose();
            handleGa("延伸閱讀");
          }}
        >
          延伸閱讀
        </NavLink>
      </Li>
      <Li>
        <a
          href="https://page.line.me/784kpkzx?openQrModal=true"
          target="_blank"
          rel="noopener noreferrer"
          onClick={() => {
            handleClose();
            handleGa("LINE立即加入好友");
          }}
        >
          <LineBtn src={lineBtn} alt="Menu LINE Btn" />
        </a>
      </Li>
    </Ul>
  );
};

export default withRouter(Menu);
