import React from "react";
import styled from "@emotion/styled";
import Step from "./Step";
import Level from "./Level";
import Challenge from "./Challenge";
import Form from "./Form";

const FlowContainer = styled.div`
  width: 90%;
  max-width: 1300px;
  margin: 120px auto 0;
  @media screen and (max-width: 767px) {
    margin: 80px auto 0;
  }
`;

const Flow = ({ selected, handleSelected }) => {
  return (
    <FlowContainer>
      <Step isSingle={selected.isSingle} handleSelected={handleSelected} />
      {selected.isSingle !== false && (
        <>
          <Level />
          <Challenge handleSelected={handleSelected} />
        </>
      )}
      {selected.challengeSelected && (
        <Form
          selected={selected}
          challengeSelected={selected.challengeSelected}
        />
      )}
    </FlowContainer>
  );
};

export default Flow;
