import React, { useEffect } from 'react';
import ScrollToTop from "../../components/ScrollToTop";
import styled from "@emotion/styled";
import logo from "../../images/cooking-logo.svg";
import ItemsWall from "./ItemsWall";
import ReactGA from "react-ga";
import fbBtn from "../../images/cooking-btn.svg";


// const Kv = styled.div`
//   width: 100%;
//   background-image: url(${bg});
//   background-size: cover;
//   background-position: center;
//   &::before {
//     content: '';
//     padding-bottom: 41.66667%;
//     display: block;
//   }
// `;

const Kv = styled.div`
  width: 80%;
  margin: auto;
  padding: 80px 0px;
  background-color: #FFF;
  @media screen and (max-width: 767px) {
    width: 82%;
  }
`;

const Title = styled.h2`
  font-weight: bold;
  font-size: 2.1875rem;
  line-height: 3.75rem;
  text-align: center;
  letter-spacing: 0.1em;
  color: #36563C;
  margin-bottom: 50px;
  img {
    max-width: 100%;
    display: block;
    margin: 10px auto 0;
  }
  @media screen and (max-width: 767px) {
    font-size: 1.375rem;
    line-height: 2.5rem;
  }
`;
const Text = styled.p`
  font-weight: 400;
  font-size: 1.125rem;
  line-height: 2.25rem;
  /* letter-spacing: 0.1rem; */
  color: #36563C;
  text-align: center;
  max-width: 888px;
  margin: auto;
  @media screen and (max-width: 767px) {
    font-size: 1rem;
    line-height: 1.6875rem;
    text-align: justify;
  }
`;

const P = styled.p`
  font-size: 1.5rem;
  font-weight: 500;
  line-height: 2.5rem;
  letter-spacing: 0.1em;
  color: #36563C;
  width: 80%;
  margin: auto;
  text-align: center;
  @media screen and (max-width: 900px) {
    font-size: 1rem;
    line-height: 1.5rem;
  }
`;

const FbBtn = styled.div`
  text-align: center;
  margin: 40px 0px 150px;
  a {
    display: inline-block;
    margin: auto;
  }
  img {
    max-width: 100%;
  }
  @media screen and (max-width: 767px) {
    margin: 40px 0px 60px;
  }
`;

const Cooking = ({ location }) => {
  useEffect(() => {
    ReactGA.pageview(location.pathname);
  }, [location])
  return (
    <main>
      <ScrollToTop />
      <Kv>
        <Title>
          歡迎光臨碳式料理
          <img src={logo} alt="" />
        </Title>
        <Text>
          獨家研發的碳式料理，前所未見的美食樣貌
          <br />
            來場獨特的餐桌體驗，顛覆你對蔬食的想像

        </Text>
      </Kv>
      <ItemsWall />
      <P>留言告訴我們＂你有多想吃碳料理＂</P>
      <FbBtn>
        <a href="https://business.facebook.com/148475335184300/posts/4149799315051862/" alt="FB按鈕" target="_blank" rel="noopener noreferrer">
          <img src={fbBtn} alt="" />
        </a>
      </FbBtn>
    </main>
  )
};

export default Cooking;
