import React from "react";
import styled from "@emotion/styled";
import openImg from "../../images/open.svg";
import phoneImg from "../../images/phone.svg";
import addrImg from "../../images/addr.svg";
import ScrollLock from "react-scrolllock";

const Container = styled.div`
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  z-index: 1;
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 10001;
`;

const Bg = styled.div`
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  background-color: rgba(0, 0, 0, 0.8);
  z-index: 1;
`;

const Box = styled.div`
  position: relative;
  pointer-events: auto;
  border-radius: 10px;
  overflow: hidden;
  background-color: #fff;
  z-index: 2;
  &::-webkit-scrollbar {
    width: 20px;
  }
  h3 {
    margin-bottom: 30px;
    font-weight: 700;
    font-size: 1.5625rem;
    line-height: 2.625rem;
    letter-spacing: 0.1em;
    color: #36563c;
  }
`;
const Area = styled.div`
  width: 350px;
  /* max-height: 500px; */
  height: 500px;
  overflow: auto;
  padding: 40px;
`;
const Item = styled.div`
  line-height: 20px;
  display: flex;
  align-items: flex-start;
  img {
    width: 20px;
    height: auto;
    margin-right: 10px;
    transform: translateY(2px);
  }
  p {
    line-height: 24px;
  }
  &:nth-of-type(2) {
    padding: 20px 0px;
  }
`;
const Close = styled.div`
  cursor: pointer;
  display: block;
  position: absolute;
  width: 100%;
  top: 20px;
  right: 20px;
  > div {
    position: relative;
    width: 30px;
    height: 30px;
    margin-left: auto;
    &::before {
      content: "";
      width: 100%;
      height: 2px;
      display: block;
      background-color: #36563c;
      position: absolute;
      top: 50%;
      transform: rotate(45deg);
    }
    &::after {
      content: "";
      width: 100%;
      height: 2px;
      display: block;
      background-color: #36563c;
      position: absolute;
      top: 50%;
      transform: rotate(-45deg);
    }
  }
`;

const MapInfo = ({ addr, openTime, phoneNumber, name, hadleCloseInfo }) => {
  return (
    <>
      <ScrollLock />
      <Container>
        <Bg onClick={hadleCloseInfo} />
        <Box>
          <Close onClick={hadleCloseInfo}>
            <div />
          </Close>
          <Area>
            <h3>{name}</h3>
            <Item>
              <img src={openImg} alt="open" />
              <div>
                {openTime.map((text, key) => (
                  <p key={key}>{text}</p>
                ))}
              </div>
            </Item>
            <Item>
              <img src={phoneImg} alt="phone" />
              <p>{phoneNumber}</p>
            </Item>
            <Item>
              <img src={addrImg} alt="addr" />
              <p>{addr}</p>
            </Item>
          </Area>
        </Box>
      </Container>
    </>
  );
};

export default MapInfo;
