import React from "react";
import styled from "@emotion/styled";
import boxTitleImg from "../../images/boxTitleImg.svg";
import boxTitleImgM from "../../images/boxTitleImg-m.svg";
import handImg from "../../images/hand.svg";
import Flow from "./Flow";

const Box = styled.div`
  width: 100%;
  background-color: #f3f6e7;
  padding-bottom: 80px;
`;

const BoxTitleImg = styled.div`
  width: 90%;
  margin: 0 auto;
  max-width: 1400px;
  &::before {
    content: "";
    width: 100%;
    padding-bottom: 20.1271129%;
    display: block;
    background-image: url(${boxTitleImg});
    background-repeat: no-repeat;
    background-position: center;
    background-size: contain;
    @media screen and (max-width: 768px) {
      background-image: url(${boxTitleImgM});
      padding-bottom: 55%;
    }
  }
`;

const FingerArea = styled.div`
  width: 90%;
  margin: 0 auto;
  max-width: 930px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  img {
    display: block;
    margin: 0 auto;
    width: 100%;
    max-width: 186px;
    @media screen and (max-width: 768px) {
      max-width: 70px;
    }
  }
`;
const Single = styled.div`
  flex: 0.44;
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  img {
    @media screen and (max-width: 768px) {
      transform: translateX(-15px);
    }
  }
`;
const Double = styled.div`
  flex: 0.44;
  display: flex;
  flex-wrap: wrap;
  img {
    &:last-of-type {
      transform: translateX(-50px);
      @media screen and (max-width: 768px) {
        transform: translateX(-15px);
      }
    }
  }
`;
const Btn = styled.div`
  cursor: pointer;
  z-index: 1;
  width: 100%;
  max-width: 400px;
  padding: 18px 10px;
  color: #fff;
  font-size: 30px;
  letter-spacing: 0.1em;
  margin-top: -30px;
  text-align: center;
  border-radius: 40px;
  background-color: ${(props) => (props.active ? "#8DC220;" : "#36563c")};
  @media screen and (max-width: 768px) {
    font-size: 20px;
    padding: 10px 4px;
    margin: -30px auto 0;
  }
`;

const Plan = ({ selected, handleSelected }) => {
  return (
    <Box>
      <BoxTitleImg />
      <FingerArea>
        <Single>
          <img src={handImg} alt="" />
          <Btn
            onClick={() =>
              handleSelected((prev) => ({
                challengeSelected: null,
                isSingle: true,
              }))
            }
            active={selected.isSingle === true}
          >
            民眾素立!
          </Btn>
        </Single>
        <Double>
          <img src={handImg} alt="" />
          <img src={handImg} alt="" />
          <Btn
            onClick={() =>
              handleSelected((prev) => ({
                challengeSelected: null,
                isSingle: false,
              }))
            }
            active={selected.isSingle === false}
          >
            團體素立!
          </Btn>
        </Double>
      </FingerArea>
      <Flow selected={selected} handleSelected={handleSelected} />
    </Box>
  );
};

export default Plan;
