import React, { useState, useRef, useEffect } from 'react';
import styled from "@emotion/styled";
import Slider from "react-slick";
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";
import axios from 'axios';
// import test from "../../test.xml";
import ReactGA from "react-ga";

// const _api = "https://cors-anywhere.small-service.gpeastasia.org/https://www.greenpeace.org/taiwan/from-the-earth/feed/";
const _api = "https://cors-anywhere.small-service.gpeastasia.org/https://www.greenpeace.org/taiwan/category/health/feed/?tag=vegetarian";

const Container = styled.div`
  width: 100%;
  overflow: hidden;
  padding: 120px 0px;
  background-color: #F3F6E7;
`;

const Title = styled.h3`
  font-weight: 500;
  font-size: 2.1875rem;
  line-height: 3.75rem;
  text-align: center;
  letter-spacing: 0.1em;
  color: #36563C;
`;

const Wall = styled.div`
  width: 100%;
  max-width: 1080px;
  margin: 60px auto 0;
  position: relative;
  @media screen and (max-width: 1024px) {
    width: 80%;
  }
  @media screen and (max-width: 767px) {
    width: 70%;
  }
`;
const Item = styled.div`
  width: 100%;
  max-width: 320px;
  padding: 0px 20px;
  img {
    width: 100%;
  }
  p {
    margin-top: 20px;
    font-weight: 500;
    font-size: 1.25rem;
    line-height: 2.125rem;
    text-align: justify;
    letter-spacing: 0.1em;
    overflow: hidden;
    display: -webkit-box;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: 2;
    a {
      color: #36563C;
    }
    @media screen and (max-width: 767px) {
      display: block;
      -webkit-box-orient: unset;
      -webkit-line-clamp: unset;
      font-size: 1.125rem;
      line-height: 1.625rem;
    }
  }
`;
const Bg = styled.div`
  width: 100%;
  background-image: url(${props => props.bg});
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  &::before {
    content: '';
    padding-bottom: 100%;
    display: block;
  };
`;

const Line = styled.div`
  width: 100%;
  height: 1px;
  background-color: #637C59;
  margin: 30px 0px;
`;

const Link = styled.a`
  width: 100%;
  display: block;
  text-align: right;
  font-size: 0.9375rem;
  line-height: 1.3125rem;
  color: #637C59;
`;

const CustonLeft = styled.div`
  cursor: pointer;
  width: 32px;
  height: 32px;
  border-left: 2px solid #6C9373;
  border-bottom: 2px solid #6C9373;
  transform: translate(-50%, -50%) rotate(45deg);
  position: absolute;
  top: 40%;
  left: -32px;
  @media screen and (max-width: 767px) {
    width: 20px;
    height: 20px;
    left: -20px;
  }
`;
const CustonRight = styled.div`
  cursor: pointer;
  width: 32px;
  height: 32px;
  border-right: 2px solid #6C9373;
  border-bottom: 2px solid #6C9373;
  transform: translate(0%, -50%) rotate(-45deg);
  position: absolute;
  top: 40%;
  right: -32px;
  @media screen and (max-width: 767px) {
    width: 20px;
    height: 20px;
    right: -20px;
  }
`;

const SwiperWall = () => {
  const [apiData, setApiData] = useState([])
  const slider = useRef(null);
  const prevRef = useRef(null);
  const nextRef = useRef(null);
  const settings = {
    dots: true,
    arrows: false,
    // centerMode: true,
    infinite: true,
    // centerPadding: "100px",
    autoplay: true,
    autoplaySpeed: 4000,
    // pauseOnHover: false,
    slidesToShow: 3,
    slidesToScroll: 1,
    speed: 500,
    responsive: [
      // {
      //   breakpoint: 1024,
      //   settings: {
      //     centerPadding: '40px',
      //   }
      // },
      {
        breakpoint: 767,
        settings: {
          // centerPadding: '25px',
          slidesToShow: 1,
        }
      }
    ]
  }
  useEffect(() => {
    axios({
      method: "GET",
      headers: { 'X-Requested-With': 'XMLHttpRequest' },
      url: _api,
      'Content-Type': 'application/json',
    })
      .then((result) => {
        const parser = new DOMParser()
        const xmlDoc = parser.parseFromString(result.data, "text/xml")


        const _itemLength = xmlDoc.getElementsByTagNameNS("*", "item").length;


        for (let i = 0; i < _itemLength; i++) {
          let _obj = {
            title: xmlDoc.getElementsByTagNameNS("*", "item").item(i).getElementsByTagName("title")[0].textContent,
            link: xmlDoc.getElementsByTagNameNS("*", "item").item(i).getElementsByTagName("link")[0].textContent,
            bg: xmlDoc.getElementsByTagNameNS("*", "item").item(i).getElementsByTagName("media:content")[0].attributes.url.textContent
          }
          setApiData((prev) => {
            return [
              ...prev,
              _obj
            ]
          })
        }

      })
      .catch((err) => { console.error(err) })
  }, [])

  return (
    <Container id="extend">
      <Title>延伸閱讀</Title>
      <Wall>
        <Slider {...settings} ref={slider}>
          {apiData.map((items, key) => {
            const { link, title, bg } = items;
            return (
              <Item key={key}>
                <Bg bg={bg} />
                <p>
                  <a href={link + "?ref=LessMeatLessHeat"} target="_blank" alt={title} rel="noopener noreferrer" onClick={() => {
                    ReactGA.event({
                      category: "Menu",
                      action: "Click",
                      label: `HomePage 延伸閱讀-${title}`
                    })
                  }}>{title}</a>
                </p>
                <Line />
                <Link href={link + "?ref=LessMeatLessHeat"} target="_blank" alt={title} rel="noopener noreferrer" onClick={() => {
                  ReactGA.event({
                    category: "Menu",
                    action: "Click",
                    label: `HomePage 延伸閱讀-${title}`
                  })
                }}>Read More ＋</Link>
              </Item>
            )
          })}

        </Slider>





        <CustonLeft ref={prevRef} onClick={() => {
          slider.current.slickPrev();
        }} />
        <CustonRight ref={nextRef} onClick={() => {
          slider.current.slickNext();
        }} />
      </Wall>



    </Container>
  )
};

export default SwiperWall;
