import React from "react";
import styled from "@emotion/styled/macro";
import MediaQuery from "react-responsive";
import beef from "../../images/beef.svg";
import lamb from "../../images/lamb.svg";
import pork from "../../images/pork.svg";
import chicken from "../../images/chicken.svg";

const Container = styled.div`
  width: 100%;
  margin: auto;
  padding: 80px 0px;
  background-color: #f3f6e7;
  > div {
    width: 80%;
    max-width: 1124px;
    margin: auto;
  }
`;

const Title = styled.h2`
  font-weight: bold;
  font-size: 2.1875rem;
  line-height: 3.75rem;
  text-align: center;
  letter-spacing: 0.1em;
  color: #36563c;
  margin-bottom: 50px;
  @media screen and (max-width: 767px) {
    font-size: 1.375rem;
    line-height: 2.5rem;
  }
`;

const Text = styled.p`
  font-weight: 400;
  font-size: 1.125rem;
  line-height: 2.25rem;
  /* letter-spacing: 0.1rem; */
  color: #36563c;
  text-align: center;
  max-width: 888px;
  margin: auto;
  @media screen and (max-width: 767px) {
    font-size: 1rem;
    line-height: 1.6875rem;
    text-align: justify;
  }
`;

const Box = styled.div`
  width: 90%;
  max-width: 800px;
  display: flex;
  justify-content: space-between;
  align-items: stretch;
  /* flex-direction: column; */
  margin: 60px auto 0px;
  flex-wrap: wrap;
`;

const Arrow = styled.div`
  position: absolute;
  bottom: 46px;
  right: 0;
  width: 15px;
  height: 15px;
  border-right: 2px solid #36563c;
  border-bottom: 2px solid #36563c;
  transform: translateY(50%) rotate(-45deg);
`;

const Items = styled.div`
  /* width: 25%; */
  align-self: flex-end;
  position: relative;
  &:nth-of-type(1) {
    width: 24.1423%;
    ${Arrow} {
      right: -6%;
    }
  }
  &:nth-of-type(2) {
    width: 20.3303%;
    ${Arrow} {
      right: -18%;
    }
  }
  &:nth-of-type(3) {
    width: 19.8221%;
    ${Arrow} {
      right: -12%;
      @media screen and (max-width: 1024px) {
        right: -18%;
      }
    }
  }
  &:nth-of-type(4) {
    width: 11.6899%;
  }
  img {
    max-width: 100%;
    display: block;
    margin: auto;
  }
  @media screen and (max-width: 767px) {
    &:nth-of-type(n + 1) {
      width: 100%;
      ${Arrow} {
        right: unset;
        bottom: -70px;
        left: 50%;
        transform: translate(-50%, -50%) rotate(45deg);
      }
    }
    &:not(:last-of-type) {
      margin-bottom: 120px;
    }
  }
`;

const Circle = styled.div`
  width: 92px;
  height: 92px;
  border-radius: 50%;
  background-color: #637c59;
  margin: 40px auto 0;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  p {
    font-weight: 500;
    font-size: 1rem;
    line-height: 1.75rem;
    text-align: center;
    letter-spacing: 0.1em;
    color: #fff;
  }
  span {
    color: #fff;
    font-weight: 500;
    font-size: 1.5rem;
    letter-spacing: 0.05em;
    line-height: 1;
    text-align: center;
  }
  @media screen and (max-width: 767px) {
    width: 80px;
    height: 80px;
    p {
      font-size: 0.875rem;
    }
    span {
      font-size: 1.25rem;
    }
  }
`;

const data = [
  {
    img: beef,
    text: "牛肉",
    kg: "60",
  },
  {
    img: lamb,
    text: "羊肉",
    kg: "24",
  },
  {
    img: pork,
    text: "豬肉",
    kg: "7",
  },
  {
    img: chicken,
    text: "雞肉",
    kg: "6",
  },
];

const Leaderboard = () => {
  return (
    <Container>
      <div>
        <Title>
          參考看看動物的碳排放
          <MediaQuery minWidth={767}>，</MediaQuery>
          <MediaQuery maxWidth={767}>
            <br />
          </MediaQuery>
          做出正確的選擇
        </Title>
        <Text>
          大部份的植物性食物都是低碳之選，碳排放比動物性食物低 10 至 50
          倍，魚類、家禽及豬肉次之，牛肉則遠遠拋離其他對手，穩佔食物最高碳排放
          No.1！如果真的好想吃肉，更友善環境的選擇先後順序。
        </Text>
        <Box>
          {data.map((items, key) => {
            const { img, text, kg } = items;
            return (
              <Items key={key}>
                <img src={img} alt="" />
                {key !== 3 && <Arrow />}
                <Circle>
                  <p>{text}</p>
                  <span>{kg}</span>
                </Circle>
              </Items>
            );
          })}
        </Box>
        <Text style={{ marginTop: "40px", textAlign: "center" }}>
          註：生產每公斤的碳排放量 / 公斤
        </Text>
        <Text style={{ marginTop: "20px", textAlign: "center" }}>
          來源：
          <a
            style={{ color: "inherit", textDecoration: "underline" }}
            href="https://ourworldindata.org/food-choice-vs-eating-local"
            target="_blank"
            rel="noreferrer"
          >
            https://ourworldindata.org/food-choice-vs-eating-local
          </a>
        </Text>
      </div>
    </Container>
  );
};

export default Leaderboard;
