import React from 'react';
import styled from "@emotion/styled/macro";

const Wrapper = styled.div`
  width: 100%;
  padding: 150px 0px;
  &:nth-of-type(1) {
    padding: 120px 0px 150px;
    @media screen and (max-width: 767px) {
      padding: 120px 0px 60px;
    }
  }
  @media screen and (max-width: 767px) {
    padding: 60px 0px;
  }
`;

const Top = styled.div`
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: stretch;
  flex-wrap: wrap;
  margin-bottom: 120px;
  @media screen and (max-width: 900px) {
    flex-direction: column-reverse;
  }
  @media screen and (max-width: 767px) {
    margin-bottom: 60px;
  }
`;
const TopImgArea = styled.div`
  width: 59%;
  z-index: 1;
  img {
    width: 100%;
  }
  @media screen and (max-width: 900px) {
    width: 100%;
  }
`;
const TopTextArea = styled.div`
  width: 33%;
  @media screen and (max-width: 900px) {
    width: 100%;
    margin-bottom: 80px;
  }
  @media screen and (max-width: 767px) {
    padding-left: 30px;
  }
`;
const Number = styled.img`
  width: 80%;
  max-width: 327px;
  transform: translateX(-60%);
  @media screen and (max-width: 900px) {
    max-width: 150px;
    transform: translateX(-60%);
  }
`;
const Line = styled.div`
  width: 100%;
  height: 1px;
  background-color: #36563C;
  margin: 45px 0px 25px;
  @media screen and (max-width: 900px) {
    width: 55%;
    margin: 25px 0px 15px;
  }
  
`;
const Sub = styled.p`
  font-size: 1.5rem;
  line-height: 2.5rem;
  letter-spacing: 0.1em;
  color: #36563C;
  @media screen and (max-width: 900px) {
    font-size: 1rem;
    line-height: 1.5rem;
  }
`;
const Title = styled.p`
  font-weight: 500;
  font-size: 2.1875rem;
  line-height: 3.75rem;
  letter-spacing: 0.1em;
  color: #36563C;
  margin-top: 40px;
  @media screen and (max-width: 1024px) {
    font-size: 2rem;
    margin-top: 30px;
  }
  @media screen and (max-width: 900px) {
    font-size: 1.5rem;
    line-height: 2rem;
  }
`;
const Bottom = styled.div`
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: stretch;
  flex-wrap: wrap;
  position: relative;
  &::before {
    content: '';
    width: 100vw;
    height: 100%;
    position: absolute;
    top: 0;
    left: calc((100vw - 100%) / 2 * -1);
    background-color: #F3F6E7;
    z-index: -1;
  }
  @media screen and (max-width: 767px) {
    padding-bottom: 80px;
    &::before {
      width: 100%;
      left: 0;
    }
  }
`;
const BottomTextArea = styled.div`
  width: 59%;
  @media screen and (max-width: 767px) {
    width: 100%;
  }
`;
const TextBox = styled.div`
  width: 80%;
  max-width: 490px;
  margin: auto;
  padding: 90px 0px;
  @media screen and (max-width: 1024px) {
    padding: 80px 0px;
  }
`;
const BottomImgArea = styled.div`
  width: 33%;
  display: flex;
  justify-content: center;
  align-items: center;
  img {
    width: 100%;
    max-width: 396px;
  }
  @media screen and (max-width: 767px) {
    width: 80%;
    display: block;
    margin: auto;
    
  }
`;
const BottomTitle = styled.p`
  font-weight: 500;
  font-size: 2.1875rem;
  line-height: 3rem;
  letter-spacing: 0.1em;
  color: #000000;
  text-align: center;
  @media screen and (max-width: 767px) {
    font-size: 1.5rem;
  }
`;
const BottomText = styled.p`
  font-size: 1.25rem;
  line-height: 2.5rem;
  text-align: justify;
  margin: 50px 0px 25px;
  @media screen and (max-width: 1024px) {
    font-size: 1.125rem;
    margin: 40px 0px 20px;
  }
  @media screen and (max-width: 767px) {
    font-size: 1rem;
    line-height: 2rem
  }
`;
const Note = styled.p`
  font-size: 0.875rem;
  text-align: justify;
  line-height: 1.75rem;
  color: #6C9373;
  @media screen and (max-width: 767px) {
    font-size: 0.75rem;
  }
`;

const Items = ({ index, num, food, foodA, foodB, title, text, season }) => {
  return (
    <Wrapper >
      <Top>
        <TopImgArea><img src={foodA} alt="" /></TopImgArea>
        <TopTextArea>
          <Number src={num} alt="01" />
          <Line />
          <Sub>主廚推薦料理</Sub>
          <Title>{food}</Title>
        </TopTextArea>
      </Top>
      <Bottom>
        <BottomTextArea>
          <TextBox>
            <BottomTitle>“ {title} ”</BottomTitle>
            <BottomText>{text}</BottomText>
            {/* {season && <Note>＊ 料理皆採用當令食材，因此會隨季節調整</Note>} */}
            {index !== 1 ?
              <Note>＊ 料理皆採用當令食材，因此會隨季節調整<br />＊ 料理皆不含真正的碳</Note>
              :
              <Note>＊ 料理皆不含真正的碳</Note>
            }
          </TextBox>
        </BottomTextArea>
        <BottomImgArea>
          <img src={foodB} alt="" />
        </BottomImgArea>
      </Bottom>
    </Wrapper >
  )
};

export default Items;
