import React from "react";
import styled from "@emotion/styled";
import useWindowResize from "../hooks/UseWindowResize";
import kvBg from "../../images/register-kv.svg";
import kvBgM from "../../images/register-kv-m.svg";

const Container = styled.div`
  width: 100%;
  background-image: url(${kvBg});
  background-size: contain;
  background-position: top;
  background-repeat: no-repeat;
  &::before {
    content: "";
    padding-bottom: 39.583333%;
    display: block;
  }
  @media screen and (max-width: 767px) {
    background-image: url(${kvBgM});
    &::before {
      padding-bottom: 177%;
    }
  }
`;

const Wall = styled.div`
  width: 100%;
  background-color: #36563c;
  padding: 120px 20px;
  text-align: center;
  p {
    font-size: 22px;
    font-weight: 500;
    line-height: 44px;
    letter-spacing: 0.1em;
    color: #fff;
  }
  @media screen and (max-width: 900px) {
    padding: 60px 30px;
    p {
      font-size: 18px;
      line-height: 36px;
    }
  }
  @media screen and (max-width: 767px) {
    text-align: justify;
    p {
      font-size: 16px;
      line-height: 26px;
    }
  }
`;

const Kv = () => {
  const mobile = useWindowResize();
  return (
    <Container>
      <Wall>
        <p>
          每一位在餐桌前的你其實都有改變世界的機會，
          {!mobile && <br />}
          少肉多蔬不僅能為身體帶來健康，還能減緩工業化畜牧對環境帶來的衝擊
        </p>
      </Wall>
    </Container>
  );
};

export default Kv;
