import React from 'react';
import styled from "@emotion/styled";
// import postLogo from "../../images/postLogo.svg";
import TreePost, { Item, MaskBox, TextBox } from "./TreePost";
import mask_a from "../../images/mask1.svg";
import mask_b from "../../images/mask2.svg";
import data from "./TreeData";
import useWindowResize from "../hooks/UseWindowResize";

const Container = styled.div`
  width: 100%;
  padding: 120px 0px;
  background-color: #F4F4F4;
  > div:not(.alone) {
    display: flex;
    position: relative;
    width: 100%;
    max-width: 1150px;
    margin: 0px auto;
    /* padding: 0px 20px; */
    @media screen and (max-width: 1024px) {
      width: 90%;
    }
    @media screen and (max-width: 767px) {
      padding: 0;
      width: 80%;
    }
  }
`;

const TimelineContainer = styled.div`
  display: flex;
  position: relative;
  width: 100%;
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 20px;
  
`;

const HalfWrapper = styled.div`
  display: flex;
  flex-direction: column;
  /* flex: 1; */
  &:nth-of-type(1) {
    width: 55%;
    ${Item} {
      justify-content: flex-start;
      &:nth-of-type(1) {
        margin-top: 150px;
      }
      &:not(:first-of-type) {
        margin-top: 150px;
      }
    }
    @media screen and (max-width: 767px) {
      width: 100%;
      ${Item} {
        justify-content: flex-end;
        &:nth-of-type(1) {
          margin-top: 0px;
        }
        &:not(:first-of-type) {
          margin-top: 80px;
        }
     }
    }
    ${MaskBox} {
      width: calc(100% - 90px);
      mask-image: url(${mask_a});
      &::before {
        padding-bottom: 72.8%;
      }
      @media screen and (max-width: 1024px) {
        width: calc(100% - 95px);
      }
      @media screen and (max-width: 768px) and (orientation: portrait) {
        width: calc(100% - 65px);
      }
      @media screen and (max-width: 767px) {
        width: calc(100% - 55px);
        mask-image: url(${mask_b});
        &::before {
          padding-bottom: 100%;
        }
      }
    }
    ${TextBox} {
      width: calc(100% - 170px);
      padding-left: 50px;
      @media screen and (max-width: 1024px) {
        padding-left: 0;
        width: calc(100% - 50px);
      }
    }
  }
  &:nth-of-type(2) {
    width: 45%;
    ${Item} {
      justify-content: flex-end;
      &:not(:first-of-type) {
        margin-top: 150px;
      }
    }
    ${MaskBox} {
      width: calc(100% - 50px);
      mask-image: url(${mask_b});
      &::before {
        padding-bottom: 100%;
      }
      @media screen and (max-width: 1024px) {
        width: calc(100% - 55px);
      }
    }
    ${TextBox} {
      width: calc(100% - 112px);
      @media screen and (max-width: 1024px) {
        width: calc(100% - 70px);
      }
    }
  }
`;

const VerticalLine = styled.div`
  position: absolute;
  top: 0;
  left: 55%;
  margin-left: -1px;
  width: 5px;
  height: 100%;
  transform: translateX(-50%);
  background: linear-gradient(to bottom, #618368 50%, #618368 50%);
  background-size: 1px 20px;
  @media (max-width: 767px) {
    left: 0px;
    margin-left: 0;
  }
`;

const AloneContainer = styled.div`
  width: calc(100% - 160px);
  padding-left: 50px;
  img {
    width: 100%;
    max-width: 380px;
  }
  h4 {
    font-weight: bold;
    font-size: 2.1875rem;
    line-height: 3.75rem;
    letter-spacing: 0.1em;
    color: #36563C
  }
  p {
    margin-top: 30px;
    font-weight: 400;
    font-size: 1.125rem;
    letter-spacing: 0.1rem;
    line-height: 2.25rem;
    color: #000;
    text-align: justify;
  }
  @media screen and (max-width: 1024px) {
    padding-left: 0;
    width: 80%;
    margin: 0 auto 60px;
    h4 {
      font-size: 1.75rem;
    }
    p {
      font-size: 1rem;
      line-height: 2rem;
    }
  }

`;

const Alone = () => {
  return (
    <AloneContainer className="alone">
      <h4>換你來出一張嘴</h4>
      <p>加入我們，從5月的碳式料理出發<br />我們將陸續舉辦各式活動<br />一路吃吃吃吃吃 吃到11月的世界無肉日<br /><br />邀你一起參與，出一張嘴用吃改變世界</p>
    </AloneContainer>
  )
};

const Tree = () => {
  const mobile = useWindowResize();

  return (
    <Container id="life">
      {mobile && <Alone />}
      <TimelineContainer>

        {!mobile ?
          <>
            <HalfWrapper>
              <Alone />
              {
                data.map((el, id) => {
                  if (id % 2 !== 1) return null;
                  return <TreePost key={id} mask={mask_a} {...el} />
                })
              }
            </HalfWrapper>
            <HalfWrapper>
              {
                data.map((el, id) => {
                  if (id % 2 !== 0) return null;
                  return <TreePost key={id} mask={mask_b} {...el} />
                })
              }
            </HalfWrapper>
          </>
          :
          <>

            <HalfWrapper>
              {
                data.map((el, id) => {
                  return <TreePost key={id} mask={mask_b} {...el} />
                })
              }
            </HalfWrapper>
          </>
        }


        <VerticalLine />
      </TimelineContainer>
    </Container>
  )
};

export default Tree;
