import React, { useEffect, useState, useRef } from "react";
import { Waypoint } from "react-waypoint";
import styled from "@emotion/styled";
import GoogleMapReact from "google-map-react";
import TwCitySelector from "tw-city-selector";
import markersIcon from "../../images/markers.svg";
import styleJson from "../../mapStyles.json";
import plus from "../../images/plus.svg";
import less from "../../images/less.svg";
import arrow from "../../images/arrow.png";
import MapInfo from "./MapInfo";

const Container = styled.div`
  width: 100%;
  padding: 120px 0px;
`;

const Title = styled.h3`
  font-weight: 500;
  font-size: 2.1875rem;
  line-height: 3.75rem;
  text-align: center;
  letter-spacing: 0.1em;
  color: #36563c;
  @media screen and (max-width: 768px) {
    font-size: 2rem;
    /* line-height: 2.5rem; */
  }
`;

const SubTitle = styled.p`
  margin-top: 5px;
  font-weight: 500;
  font-size: 1.125rem;
  line-height: 2.25rem;
  text-align: center;
  letter-spacing: 0.1em;
  color: #000000;
  span {
    span {
      cursor: pointer;
      color: #2d5ebd;
      text-decoration: underline;
    }
  }
  @media screen and (max-width: 767px) {
    font-size: 1rem;
    width: 90%;
    margin: auto;
  }
`;

const MapContainer = styled.div`
  margin: 60px auto 0;
  position: relative;
  width: 100%;
  &::before {
    content: "";
    padding-bottom: 58.5%;
    display: block;
    @media screen and (max-width: 767px) {
      padding-bottom: 150%;
    }
  }
  /* > div:not(:last-of-type) {
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
  } */
`;

const MapControl = styled.div`
  position: absolute;
  bottom: 50px;
  right: 30px;
  img {
    cursor: pointer;
    width: 40px;
    height: 40px;
    display: block;
    &:first-of-type {
      margin-bottom: 10px;
    }
  }
`;

const MarkerCss = styled.div`
  width: 20px;
  height: 30px;
  position: relative;
  img {
    width: 20px;
    height: 30px;
  }
  p {
    position: absolute;
    left: 50%;
    top: 30px;
    transform: translateX(-50%);
    font-weight: bold;
    color: #36563c;
    white-space: nowrap;
  }
`;

const SelectBox = styled.div`
  max-width: 500px;
  margin: 40px auto 0;
  display: flex;
  justify-content: center;
  opacity: ${(props) => (props.show ? 1 : 0)};
  transition: all 0.5s;
  select {
    padding: 4px 15px 4px 6px;
    border-radius: 0px;
    color: #000;
    font-size: 16px;
    border: none;
    border-bottom: 1px solid;
    appearance: none;
    background-color: transparent;
    background-image: url(${arrow});
    background-size: 8px;
    background-repeat: no-repeat;
    background-position: right center;
    &:nth-of-type(1) {
      margin-right: 5px;
    }
  }
`;

const SearchBtn = styled.button`
  cursor: pointer;
  margin-left: 20px;
  width: 60px;
  border-radius: 4px;
  font-size: 16px;
  color: #fff;
  background-color: #36563c;
`;

const apiKey =
  process.env.NODE_ENV === "development"
    ? "AIzaSyCtmeI40OQb_cRsYArtlIqIqZ0IjS4Il88"
    : "AIzaSyCs0r9YHif0SonYi2a0QGG_Tv0hiQ8vS5w";

const RecommendMap = () => {
  const [mapApiLoaded, setMapApiLoaded] = useState(false);
  const mapInstance = useRef();
  const mapApi = useRef();

  // const [mapInstance, setMapInstance] = useState(null);
  // const [mapApi, setMapApi] = useState(null);
  const firstRef = useRef(true);
  const [showSelect, setShowSelect] = useState(false);

  const [currentZoom, setCurrentZoom] = useState(null);
  const [places, setPlaces] = useState([]);

  const [county, setCounty] = useState("");
  const [district, setDistrict] = useState("");

  const [defaultCenter, setDefaultCenter] = useState({
    lat: 25.042737,
    lng: 121.5089866,
  });

  const MapOptions = {
    styles: styleJson,
    maxZoom: 20,
    minZoom: 12,
    mapTypeControl: false,
    zoomControl: false,
    fullscreenControl: false,
    streetViewControl: false,
    scrollwheel: false,
  };
  const [info, setInfo] = useState({
    addr: "",
    openTime: [],
    phoneNumber: "",
    name: "",
  });
  const [showInfo, setShowInfo] = useState(false);

  useEffect(() => {
    if (firstRef.current) {
      new TwCitySelector({
        el: ".form-inline",
        elCounty: ".county",
        elDistrict: ".district",
      });
      firstRef.current = false;
    }
  }, []);

  const handleApiLoaded = (map, maps) => {
    //載入完成
    mapInstance.current = map;
    mapApi.current = maps;

    setMapApiLoaded((prev) => {
      return !prev;
    });
    setCurrentZoom(map.getZoom());
    // textSearch(defaultCenter);
    // const service = new maps.places.PlacesService(map);
    // const request = {
    //   location: defaultCenter,
    //   radius: "500",
    //   query: "蔬食餐廳",
    // };
    // service.textSearch(request, (results, status, pagination) => {
    //   const d = [];
    //   if (status === maps.places.PlacesServiceStatus.OK) {
    //     d.push(results);
    //     setPlaces((prev) => [...prev].concat(results));
    //     if (pagination.hasNextPage) {
    //       pagination.nextPage();
    //     }
    //   }
    // });
  };

  // const fetchPlaces = (lat, lng) => {
  //   const service = new mapApi.current.places.PlacesService(
  //     mapInstance.current
  //   );
  //   const request = {
  //     location: {
  //       lat,
  //       lng,
  //     },
  //     radius: "500",
  //     query: "蔬食餐廳",
  //   };
  //   service.textSearch(request, (results, status, pagination) => {
  //     const d = [];
  //     if (status === mapApi.current.places.PlacesServiceStatus.OK) {
  //       d.push(results);
  //       setPlaces((prev) => [...prev].concat(results));
  //       if (pagination.hasNextPage) {
  //         pagination.nextPage();
  //       }
  //     }
  //   });
  // };

  const zoomControl = (trigger) => {
    //true: 放大， false: 縮小
    if (mapApiLoaded) {
      trigger
        ? mapInstance.current.setZoom(mapInstance.current.getZoom() + 1)
        : mapInstance.current.setZoom(mapInstance.current.getZoom() - 1);
    }
  };

  const checkPosition = () => {
    if (navigator.geolocation) {
      navigator.geolocation.getCurrentPosition(
        function (position) {
          // const lat = position.coords.latitude.toFixed(2);
          // const lng = position.coords.longitude.toFixed(2);
          setDefaultCenter({
            lat: parseFloat(position.coords.latitude),
            lng: parseFloat(position.coords.longitude),
          });

          textSearch({
            lat: parseFloat(position.coords.latitude),
            lng: parseFloat(position.coords.longitude),
          });
        },
        function error(err) {
          textSearch(defaultCenter);
        }
      );
    } else {
    }
  };
  const gep = (map, maps, id, address, name) => {
    const request = {
      placeId: id,
      fields: ["opening_hours", "formatted_phone_number"],
    };
    const service = new maps.places.PlacesService(map);
    service.getDetails(request, (results, status) => {
      // open: results.opening_hours.weekday_text,
      // phone: results.formatted_phone_number
      // console.log(results)
      setInfo({
        addr: address,
        openTime: results.opening_hours
          ? results.opening_hours.weekday_text
          : ["目前沒有提供"],
        phoneNumber: results.formatted_phone_number
          ? results.formatted_phone_number
          : "目前沒有提供",
        name: name,
      });
    });
  };

  const textSearch = ({ lat, lng }) => {
    setPlaces([]);
    const service = new mapApi.current.places.PlacesService(
      mapInstance.current
    );
    const request = {
      location: { lat, lng },
      radius: "500",
      query: "蔬食餐廳",
    };
    service.textSearch(request, (results, status, pagination) => {
      const d = [];
      if (status === mapApi.current.places.PlacesServiceStatus.OK) {
        d.push(results);
        setPlaces((prev) => [...prev].concat(results));
        if (pagination.hasNextPage) {
          pagination.nextPage();
        }
      }
    });
  };
  const fetchCountyArea = () => {
    if (
      county.indexOf("選擇") >= 0 ||
      district.indexOf("選擇") >= 0 ||
      county === "" ||
      district === ""
    ) {
      alert("請選擇縣市和區域");
      return;
    }
    const taiwanLocation = new mapApi.current.Geocoder(mapInstance.current);
    const request = {
      address: county + district,
    };
    taiwanLocation.geocode(request, (results, status) => {
      const lat = results[0].geometry.location.lat();
      const lng = results[0].geometry.location.lng();
      setDefaultCenter({
        lat,
        lng,
      });
      textSearch({ lat, lng });
    });
  };

  const fetchInfo = (id, address, name) => {
    gep(mapInstance.current, mapApi.current, id, address, name);
    setShowInfo(true);
  };

  const hadleCloseInfo = () => {
    setShowInfo(false);
  };

  return (
    <Waypoint
      bottomOffset="10%"
      scrollableAncestor={window}
      onEnter={checkPosition}
    >
      <Container id="recommend">
        <Title>尋找附近哪裡有蔬食吧!</Title>
        <SubTitle>
          改打開定位系統尋找看看附近的蔬食餐廳
          <br />
          點選地標可顯示餐廳資訊
          <br />
          <span>
            如果想查看其他地區，可以點選
            <span
              onClick={() => {
                setShowSelect(true);
              }}
            >
              搜尋其他地區
            </span>
          </span>
        </SubTitle>
        <SelectBox className="form-inline" show={showSelect}>
          <select
            className="county"
            onChange={(event) => {
              setCounty(event.target.value);
            }}
          ></select>
          <select
            className="district"
            onChange={(event) => {
              setDistrict(event.target.value);
            }}
          ></select>
          <SearchBtn type="button" onClick={fetchCountyArea}>
            搜尋
          </SearchBtn>
        </SelectBox>
        <MapContainer>
          {showInfo && <MapInfo {...info} hadleCloseInfo={hadleCloseInfo} />}
          <div
            id="map"
            style={{
              width: "100%",
              height: "100%",
              position: "absolute",
              top: 0,
              left: 0,
            }}
          >
            <GoogleMapReact
              bootstrapURLKeys={{
                key: apiKey,
                libraries: ["places"],
              }}
              center={defaultCenter}
              defaultZoom={16}
              yesIWantToUseGoogleMapApiInternals
              options={MapOptions}
              onZoomAnimationStart={(e) => {
                setCurrentZoom(e);
              }}
              onGoogleApiLoaded={({ map, maps }) => {
                handleApiLoaded(map, maps);
              }}
            >
              {places.map((items, key) => {
                const { formatted_address, name, geometry, place_id } = items;
                return (
                  <MarkerCss
                    key={key}
                    lat={geometry.location.lat()}
                    lng={geometry.location.lng()}
                    onClick={() => {
                      fetchInfo(place_id, formatted_address, name);
                    }}
                  >
                    <img src={markersIcon} alt="" />
                    {currentZoom >= 18 && <p>{name}</p>}
                  </MarkerCss>
                );
              })}
            </GoogleMapReact>
            <MapControl>
              <img
                src={plus}
                onClick={() => {
                  zoomControl(true);
                }}
                alt="放大"
              />
              <img
                src={less}
                onClick={() => {
                  zoomControl(false);
                }}
                alt="縮小"
              />
            </MapControl>
          </div>
        </MapContainer>
      </Container>
    </Waypoint>
  );
};

export default RecommendMap;
