import React from "react";
import styled from "@emotion/styled";
import logo from "../../images/party-logo.svg";
import party1 from "../../images/party1.svg";
import party2 from "../../images/party2.svg";
import party3 from "../../images/party3.svg";
import pl from "../../images/pl.svg";
import pr from "../../images/pr.svg";
// import fbBtnImg from "../../images/fbBtn.svg";
import ReactGA from "react-ga";
import { Kv } from "./Party";
import MediaQuery from "react-responsive";

const Container = styled.div`
  width: 100%;
  margin: auto;
  padding: 120px 0px;
  background-color: #fff;
  > div {
    width: 90%;
    max-width: 1024px;
    margin: auto;
    display: flex;
    flex-direction: column;
    @media screen and (max-width: 767px) {
      width: 85%;
    }
  }
`;

const Title = styled.h2`
  font-weight: bold;
  font-size: 2.1875rem;
  line-height: 3.75rem;
  text-align: center;
  letter-spacing: 0.1em;
  color: #36563c;
  margin-bottom: 50px;
  img {
    max-width: 100%;
    display: block;
    margin: 10px auto 0;
  }
  @media screen and (max-width: 767px) {
    font-size: 1.375rem;
    line-height: 2.5rem;
  }
`;
const Text = styled.p`
  font-weight: 400;
  font-size: 1.125rem;
  line-height: 2.25rem;
  /* text-align: center; */
  /* letter-spacing: 0.1rem; */
  color: #36563c;
  /* text-align: center; */
  /* max-width: 888px; */
  margin: auto;
  @media screen and (max-width: 767px) {
    font-size: 0.875rem;
    line-height: 1.6875rem;
    text-align: justify;
  }

  a {
    text-decoration: underline;
    color: #36563c;
    font-weight: bold;
    word-break: break-all;
  }
`;

const Box = styled.div`
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
  margin: 90px auto 0px;
  flex-wrap: wrap;
`;

const Items = styled.div`
  width: 26%;
  img {
    max-width: 100%;
    display: block;
    margin: auto;
  }
  @media screen and (max-width: 1024px) {
    width: 28%;
  }
  @media screen and (max-width: 767px) {
    width: 100%;
    &:not(:last-of-type) {
      margin-bottom: 80px;
    }
    img {
      max-width: 50%;
    }
  }
`;
const ItemsTitle = styled.div`
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 30px;
  h3 {
    font-weight: bold;
    font-size: 2rem;
    line-height: 3.25rem;
    text-align: center;
    letter-spacing: 0.1em;
    color: #36563c;
    margin: 0px 15px;
  }

  &::before {
    content: "";
    width: 34px;
    height: 34px;
    display: inline-block;
    background-image: url(${pl});
    background-size: cover;
    background-position: center;
    transform: translateY(8px);
  }
  &::after {
    content: "";
    width: 34px;
    height: 34px;
    display: inline-block;
    background-image: url(${pr});
    background-size: cover;
    background-position: center;
    transform: translateY(8px);
  }
  @media screen and (max-width: 1024px) {
    h3 {
      font-size: 1.75rem;
    }
  }
  @media screen and (max-width: 768px) {
    h3 {
      font-size: 1.5rem;
      margin: 0px 8px;
    }
    &::before,
    &::after {
      transform: translateY(5px);
    }
  }
  @media screen and (max-width: 767px) {
    margin-top: 20px;
    h3 {
      font-size: 1.25rem;
      line-height: 2.25rem;
    }
    &::before,
    &::after {
      width: 25px;
      height: 25px;
    }
  }
`;

