import React from "react";
import styled from "@emotion/styled";
import { data } from "./constants ";
import hand from "../../images/little-hand.svg";

const Box = styled.div`
  width: 100%;
  background-color: #fff;
  padding: 80px 0px;
`;

const Container = styled.div`
  width: 90%;
  margin: 0 auto;
  max-width: 1200px;
`;

const Title = styled.h3`
  font-size: 35px;
  text-align: center;
  line-height: 65px;
  color: #36563c;
  letter-spacing: 0.1em;
  span {
    font-size: 72px;
    margin: 0px 5px;
  }
  @media screen and (max-width: 767px) {
    font-size: 24px;
    line-height: 40px;
    span {
      display: block;
      font-size: 48px;
    }
  }
`;

const PostWall = styled.div`
  width: 100%;
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
`;

const PostItems = styled.div`
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 30px 0px;
  > div {
    &:first-of-type {
      max-width: 480px;
      flex: 0.4;
    }
    &:last-of-type {
      max-width: 630px;
      flex: 0.55;
    }
    img {
      width: 100%;
    }
  }
  &:nth-of-type(even) {
    flex-direction: row-reverse;
  }
  &:not(:last-of-type) {
    border-bottom: 3px dashed #c4c4c4;
  }
`;

const Wall = styled.div`
  width: 100%;
  background-color: #36563c;
  padding: 120px 20px;
  text-align: center;
  display: flex;
  justify-content: center;
  align-items: center;
  img {
    width: 50px;
    @media screen and (max-width: 767px) {
      width: 30px;
    }
  }
  p {
    font-size: 22px;
    font-weight: 500;
    line-height: 44px;
    letter-spacing: 0.1em;
    color: #fff;
    margin: 0 20px;
  }
  @media screen and (max-width: 900px) {
    padding: 60px 30px;
    p {
      font-size: 18px;
      line-height: 36px;
    }
  }
  @media screen and (max-width: 767px) {
    text-align: justify;
    p {
      font-size: 16px;
      line-height: 26px;
    }
  }
`;

const Change = () => {
  return (
    <>
      <Box>
        <Container>
          <Title>
            你絕對沒想到！
            <br />
            若全台灣所有人一起<span>吃素1天</span>能減少的碳排放量等同於...
          </Title>
          <PostWall>
            {data.map(({ area, info }, index) => (
              <PostItems key={index}>
                <div>
                  <img src={area} alt="area" />
                </div>
                <div>
                  <img src={info} alt="info" />
                </div>
              </PostItems>
            ))}
          </PostWall>
        </Container>
      </Box>
      <Wall>
        <img src={hand} alt="little-hand" />
        <p>一天由肉食改成素食，減去的碳排約 2.2 公斤/人</p>
        <img src={hand} alt="little-hand" />
      </Wall>
    </>
  );
};

export default Change;
