import styled from "@emotion/styled";
import arrow from "../../../images/arrow.png";

export const Box = styled.div`
  width: 100%;
  position: relative;
`;

export const FormBox = styled.div`
  width: 90%;
  margin: 0 auto;
  border-radius: 20px;
  border: 3px solid #000;
  position: relative;
  form {
    width: 90%;
    max-width: 720px;
    margin: 0 auto;
    padding: 30px 0;
    > p {
      font-size: 25px;
      letter-spacing: 0.1em;
      color: #36563c;
      margin-bottom: 30px;
      @media screen and (max-width: 767px) {
        font-size: 20px;
      }
    }
  }
`;

export const ListSingle = styled.div`
  width: 100%;
  &:not(:last-of-type) {
    margin-bottom: 50px;
    @media screen and (max-width: 767px) {
      margin-bottom: 30px;
    }
  }
`;
export const ListMultiple = styled(ListSingle)`
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: flex-start;
  > div {
    flex: 0.45;
    @media screen and (max-width: 767px) {
      flex: unset;
      width: 100%;
      &:first-of-type {
        margin-bottom: 30px;
      }
    }
  }
`;
export const FormItems = styled.div`
  width: 100%;
  position: relative;
  p {
    color: #000;
    font-size: 25px;
    @media screen and (max-width: 767px) {
      padding-left: 15px;
      font-size: 18px;
    }
  }
  input,
  select {
    width: 100%;
    background-color: #e0eed7;
    border-radius: 20px;
    border: none;
    outline: none;
    margin-top: 10px;
    font-size: 22px;
    padding: 5px 15px;
    @media screen and (max-width: 767px) {
      font-size: 18px;
    }
  }
  select {
    color: #000;
    appearance: none;
    background-image: url(${arrow});
    background-size: 12px;
    background-repeat: no-repeat;
    background-position: 95% center;
  }
`;

export const Suggestion = styled.span`
  position: absolute;
  bottom: -30px;
  left: 0;
  font-size: 16px;
  color: red;
  cursor: pointer;
`;

export const CheckEl = styled.label`
  display: flex;
  align-items: center;
  cursor: pointer;
  > div {
    position: relative;
    width: 24px;
    height: 24px;
    border: 2px solid #646464;
    background-color: #e3e3e2;
    border-radius: 50%;
    &::before {
      content: "";
      opacity: 0;
      width: 12px;
      height: 12px;
      background-color: #646464;
      border-radius: 50%;
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
    }
  }
  p {
    font-size: 25px;
    line-height: 32px;
    color: #646464;
    letter-spacing: 0.1em;
    margin-bottom: 2px;
    margin-left: 4px;
    @media screen and (max-width: 767px) {
      font-size: 16px;
      line-height: 24px;
      flex: 1;
      text-align: justify;
    }
  }

  input[type="checkbox"],
  input[type="radio"] {
    display: none;
  }
  input[type="checkbox"]:checked + div,
  input[type="radio"]:checked + div {
    &::before {
      opacity: 1;
    }
  }
`;

export const CheckElB = styled(CheckEl)`
  align-items: flex-start;
  > div {
    transform: translateY(4px);
  }
  p {
    font-size: 16px;
    line-height: 24px;
    margin-left: 10px;
    flex: 1;
  }
  a {
    color: #6c9373;
    text-decoration: underline;
  }
`;

export const Btn = styled.button`
  cursor: pointer;
  width: 260px;
  margin: 40px auto 0;
  display: block;
  border-radius: 40px;
  font-size: 24px;
  text-align: center;
  letter-spacing: 0.1em;
  color: #ffffff;
  background-color: #6c9373;
  padding: 15px 10px;
  @media screen and (max-width: 767px) {
    width: 160px;
    padding: 10px;
    font-size: 18px;
  }
`;

export const ForSingle = styled.div`
  display: flex;
  align-items: center;
  justify-content: center;
  img {
    &:last-of-type {
      transform: scale(-1, 1);
    }
  }
  @media screen and (max-width: 767px) {
    margin-bottom: 40px;
    img {
      display: none;
    }
  }
`;
export const Mirroring = styled.img`
  width: 201px;
  height: auto;
`;

export const Session = styled.div`
  font-weight: bold;
  font-size: 25px;
  text-align: center;
  letter-spacing: 0.1em;
  margin: 0 30px;
  p {
    color: #231916;
  }
  div {
    color: #ffffff;
    background-color: #6c9373;
    padding: 10px;
    border-radius: 40px;
    width: 100%;
    min-width: 300px;
    margin-top: 15px;
  }
  @media screen and (max-width: 767px) {
    font-size: 18px;
    div {
      min-width: 240px;
    }
  }
`;

export const Flag = styled.div`
  display: flex;
  justify-content: center;
  align-items: flex-start;
  margin-bottom: 40px;
  > div {
    &:first-of-type {
      width: 60%;
    }
    &:last-of-type {
      width: 40%;
      img {
        max-width: 100%;
        display: block;
        margin: 0 auto;
      }
    }
  }
  @media screen and (max-width: 767px) {
    flex-wrap: wrap;
    flex-direction: column-reverse;
    > div {
      &:first-of-type {
        width: 100%;
      }
      &:last-of-type {
        width: 100%;
      }
    }
    label {
      margin-top: 10px;
    }
  }
`;
export const Ask = styled.p`
  color: #000;
  font-size: 25px;
  @media screen and (max-width: 767px) {
    font-size: 18px;
    margin-top: 20px;
  }
`;

export const RadioBox = styled.div`
  width: 100%;
  margin-top: 20px;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  label {
    &:first-of-type {
      margin-right: 40px;
      @media screen and (max-width: 767px) {
        margin-right: 20px;
      }
    }
  }
`;

export const FireLeft = styled.div`
  width: 12%;
  position: absolute;
  top: 0;
  left: 0;
  img {
    width: 100%;
  }
  @media screen and (max-width: 1000px) {
    display: none;
  }
`;

export const FireRight = styled.div`
  width: 12%;
  position: absolute;
  top: 0;
  right: 0;
  img {
    width: 100%;
  }
  @media screen and (max-width: 1000px) {
    display: none;
  }
`;
