import React from 'react';
import styled from "@emotion/styled";
import fb from "../images/fb.svg";
import ig from "../images/ig.svg";
import yt from "../images/yt.svg";

const Container = styled.footer`
  width: 100%;
  padding: 40px;
  background-color: #36563C;
  @media screen and (max-width: 768px) {
    padding: 40px 30px;
  }
`;

const Top = styled.div`
  li {
    display: inline-block;
    padding: 0px 10px;
    &:first-of-type {
      padding-left: 0px;
    }
    &:not(:last-child) {
      border-right: 1px solid #FFF;
      a {
        font-size: 1rem;
        line-height: 1.5rem;
        letter-spacing: 0.1em;
        color: #FFFFFF;
      }
    }
    &:last-child {
      > div {
        display: flex;
        align-items: center;
        > div {
          margin-left: 10px;
          display: flex;
          align-items: center;
          @media screen and (max-width: 768px) {
            margin-left: 0px;
          }
        }
      }
      p {
        font-size: 1rem;
        line-height: 1.5rem;
        letter-spacing: 0.1em;
        color: #FFFFFF;
      }
      a {
        &:last-child {
          margin-left: 5px;
        }
      }
      img {
        width: 30px;
      }
      @media screen and (max-width: 768px) {
        margin-top: 30px;
        padding-left: 0;
        display: block;
        > div {
          align-items: flex-start;
          flex-direction: column;
          > div {
            margin-top: 10px;
          }
        }
        a {
          margin-right: 15px;
        }
      }
    }
  }
`;

const Bottom = styled.p`
  margin-top: 20px;
  font-size: 1rem;
  line-height: 1.5rem;
  letter-spacing: 0.1em;
  color: #FFFFFF;
  @media screen and (max-width: 768px) {
    margin-top: 80px;
  }
`;


const data = {
  "public": [
    {
      a: 1
    }
  ],
  "private": [
    {
      icon: fb,
      link: 'https://www.facebook.com/greenpeace.org.tw/'
    },
    {
      icon: ig,
      link: 'https://www.instagram.com/greenpeace_tw/'
    },
    {
      icon: yt,
      link: 'https://www.youtube.com/channel/UCuxqIuQqkmKzaf4q3RnXqWg'
    }
  ]
}

const Footer = () => {
  return (
    <Container>
      <Top>
        <ul>
          <li>
            <a href="https://www.greenpeace.org/taiwan/policies/privacy-and-cookies/" alt="隱私政策" target="_blank" rel="noopener noreferrer">隱私政策</a>
          </li>
          <li>
            <a href="https://www.greenpeace.org/taiwan/policies/terms-and-conditions/" alt="版權聲明" target="_blank" rel="noopener noreferrer">版權聲明</a>
          </li>
          {/* <li>
            <a href="" alt="使用條款" target="_blank" rel="noopener noreferrer">使用條款</a>
          </li> */}
          <li>
            <div>
              <p>關注我們</p>
              <div>
                {data["private"].map(({ icon, link }, key) => (
                  <a href={link} key={key} rel="noopener noreferrer">
                    <img src={icon} alt="" />
                  </a>
                ))}
              </div>
            </div>
          </li>
        </ul>
      </Top>
      <Bottom>版權所有 © GREENPEACE 2021</Bottom>
    </Container>
  )
};

export default Footer;
