import React, { useState } from "react";
import styled from "@emotion/styled";
import useInterval from "../hooks/useInterval";
import { Link } from "react-router-dom";
import { colors } from "./constants";
import { keyframes } from "@emotion/react";
import ReactGA from "react-ga";
import loopImg from "../../images/loop-2.png";
import kv2Title from "../../images/kv2Title.svg";
import kv2TitleM from "../../images/kv2Title-m.svg";
import kvInfo from "../../images/kv-info.svg";
import kvInfoM from "../../images/kv-info-m.svg";
import useWindowResize from "../hooks/UseWindowResize";
import fire from "../../images/kv-fire-1.svg";

import whyA from "../../images/why-a.jpg";
import whyB from "../../images/why-b.jpg";
import more from "../../images/more.svg";

const Container = styled.div`
  width: 100%;
  height: calc(100vh - 80px);
  position: relative;
  background-color: #36563c;
  /* transition: all 0.5s; */
  overflow: hidden;
`;

const loop = keyframes`
 from {
    background-position: 0 0;
  }
  to {
    background-position: 600% 0%;
  }
`;
const Hand = styled.div`
  width: 40%;
  max-width: 773px;
  background-size: auto 100%;
  background-image: url(${loopImg});
  animation: ${loop} 1.5s steps(6) infinite;
  position: absolute;
  bottom: 0;
  left: 0;
  z-index: 2;
  &:before {
    content: "";
    padding-bottom: 97.0245796%;
    display: block;
  }
  @media screen and (min-width: 1500px) and (max-height: 800px) {
    width: 34%;
    left: 4%;
  }
  @media screen and (max-width: 1000px) {
    width: 65%;
    left: 44%;
    max-width: 353px;
    transform: translateX(-50%);
    z-index: 1;
  }
  @media screen and (max-height: 600px) {
    width: 46%;
  }
`;

const Title = styled.img`
  width: 40%;
  position: absolute;
  top: 0;
  left: 50%;
  transform: translateX(-50%);
  /* @media screen and (min-width: 1500px) and (max-height: 800px) {
    width: 30%;
  } */
  @media screen and (max-width: 1000px) {
    top: 30px;
    min-width: 300px;
  }
  @media screen and (max-width: 767px) and (max-height: 600px) {
    top: 10px;
    min-width: 260px;
  }
`;

const Info = styled.img`
  position: absolute;
  top: 63%;
  left: 65%;
  transform: translate(-50%, -50%);
  width: 26%;
  max-width: 500px;
  min-width: 500px;
  @media screen and (min-width: 1500px) and (max-height: 800px) {
    top: 67%;
    max-width: 450px;
    min-width: 450px;
  }
  @media screen and (max-width: 1440px) {
    top: 66%;
    max-width: 400px;
    min-width: 400px;
  }
  @media screen and (max-width: 1000px) {
    top: 50%;
    left: 50%;
    max-width: 300px;
    min-width: 300px;
  }
  @media screen and (max-width: 767px) and (max-height: 600px) {
    top: 270px;
    max-width: 250px;
    min-width: 250px;
  }
`;

const Fire1 = styled.img`
  position: absolute;
  max-width: 180px;
  top: 0%;
  left: 3%;
  @media screen and (max-width: 767px) {
    max-width: 80px;
    top: 9%;
    left: -7%;
  }
`;
const Fire2 = styled.img`
  position: absolute;
  max-width: 180px;
  bottom: -5%;
  left: 40%;
  @media screen and (max-width: 767px) {
    max-width: 120px;
    bottom: -5%;
    right: -6%;
    left: unset;
  }
`;
const Fire3 = styled.img`
  position: absolute;
  max-width: 110px;
  right: 11%;
  top: 33%;
  @media screen and (max-width: 767px) {
    max-width: 80px;
    right: -6%;
    top: -3%;
  }
`;

const TextArea = styled.div`
  width: 100%;
  background-color: #6c9373;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 120px 0px;
  @media screen and (max-width: 768px) {
    padding: 100px 70px;
  }
  p　 {
    font-weight: 500;
    font-size: 1.375rem;
    line-height: 2.75rem;
    color: #fff;
    text-align: center;
    @media screen and (max-width: 768px) {
      font-size: 1.125rem;
      line-height: 2.25rem;
      text-align: justify;
    }
  }
  span {
    @media screen and (min-width: 768px) {
      display: block;
    }
  }
`;

const WhyContainer = styled.div`
  position: relative;
  width: 100%;
  background-image: url(${whyA});
  background-size: cover;
  background-position: top;
  background-repeat: no-repeat;
  > div {
    width: 50%;
    margin-left: auto;
    background-color: rgba(37, 61, 42, 0.8);
    padding: 100px;
  }
  @media screen and (max-width: 768px) {
    background-image: url(${whyB});
    background-size: auto 100%;
    &::before {
      content: "";
      padding-bottom: 240%;
      display: block;
    }
    > div {
      width: 100%;
      position: absolute;
      bottom: 0;
      left: 0;
      padding: 60px;
    }
  }
`;

const WhyTitle = styled.h3`
  font-weight: 700;
  font-size: 2.1875rem;
  line-height: 3.75rem;
  letter-spacing: 0.1rem;
  color: #ebefd9;
  margin-bottom: 50px;
  @media screen and (max-width: 768px) {
    font-size: 1.375rem;
    line-height: 2.5rem;
  }
`;

const WhyP = styled.p`
  font-weight: 400;
  font-size: 1.125rem;
  line-height: 2.25rem;
  letter-spacing: 0.1rem;
  color: #ffffff;
  max-width: 388px;
  text-align: justify;
  @media screen and (max-width: 768px) {
    font-size: 1rem;
    line-height: 1.6875rem;
  }
`;

const Line = styled.div`
  width: 100%;
  height: 1px;
  background-color: rgba(255, 255, 255, 0.7);
  margin: 30px 0px;
`;

const MoreBtn = styled.div`
  text-align: right;
  img {
    width: 82px;
    @media screen and (max-width: 767px) {
      width: 72px;
    }
  }
`;

const Kv2 = () => {
  const [count, setCount] = useState(0);
  const mobile = useWindowResize(1000);
  useInterval(() => {
    setCount(count + 1);
  }, 1500);
  let bgColor = colors[count % colors.length];
  return (
    <>
      <Container style={{ backgroundColor: bgColor }}>
        <Title src={!mobile ? kv2Title : kv2TitleM} alt="kv title" />
        <Hand />
        <Link
          to="/register"
          onClick={() => {
            ReactGA.event({
              category: "Menu",
              action: "Click",
              label: "HomePage 全里素立-立即報名",
            });
          }}
        >
          <Info src={!mobile ? kvInfo : kvInfoM} alt="kv-info" />
        </Link>
        <Fire1 src={fire} alt="fire" />
        <Fire2 src={fire} alt="fire" />
        <Fire3 src={fire} alt="fire" />
      </Container>
      <TextArea>
        <p>每一位在餐桌前的你，其實都擁有改變世界的機會。</p>
      </TextArea>
      <WhyContainer id="why">
        <div>
          <WhyTitle>為何減碳很重要</WhyTitle>
          <WhyP>
            若你願意，少肉多蔬的選擇就能為環境保護出一份力，減緩氣候變遷、友善生態。今天就開始吧！
          </WhyP>
          <Line />
          <MoreBtn>
            <Link
              to="/reason/"
              onClick={() => {
                ReactGA.event({
                  category: "Menu",
                  action: "Click",
                  label: "HomePage 為何減碳-了解更多",
                });
              }}
            >
              <img src={more} alt="more img" />
            </Link>
          </MoreBtn>
        </div>
      </WhyContainer>
    </>
  );
};

export default Kv2;
