import React, {
  useState,
  useEffect,
  useCallback,
  useMemo,
  useRef,
} from "react";
import { sendPetitionTracking } from "../../utility/sendPetitionTracking";
import axios from "axios";
import Swal from "sweetalert2/dist/sweetalert2.js";
import "sweetalert2/dist/sweetalert2.min.css";
import {
  Box,
  FormBox,
  ListSingle,
  ListMultiple,
  FormItems,
  Suggestion,
  CheckEl,
  Btn,
  ForSingle,
  Mirroring,
  Session,
  Flag,
  Ask,
  RadioBox,
  FireLeft,
  FireRight,
  CheckElB,
} from "./styles";
import {
  domains,
  topLevelDomains,
  checkEmail,
  checkPhone,
} from "../../utility/checkForm";
import FlowContainer from "./FlowContainer";
import grass from "../../images/grass.svg";
import corn from "../../images/corn.svg";
import mirroring from "../../images/left-hand.svg";
import flagImg from "../../images/flag.jpg";
import fireL from "../../images/fire-left.svg";
import fireR from "../../images/fire-right.svg";

const Form = ({ selected, challengeSelected }) => {
  const [suggestionText, setSuggestion] = useState("");
  const isSent = useRef(false);
  const sentBtn = useRef(null);
  const [emailCheck, setEmailCheck] = useState(false);
  const [phoneCheck, setPhoneCheck] = useState(false);
  const [flagRadio, setFlagRadio] = useState(null);
  const optionsNum = useRef(100);
  const lastName = useRef(null);
  const firstName = useRef(null);
  const email = useRef(null);
  const phone = useRef(null);
  const birthdate = useRef(null);
  const team = useRef(null);
  const privacy = useRef(null);

  let optionsItemArr = useMemo(() => {
    let currYear = new Date().getFullYear();
    return Array.from({ length: optionsNum.current })
      .fill({ years: currYear })
      .map(({ years }, index) => (
        <option value={`${years - index}-01-01`} key={index}>
          {years - index}
        </option>
      ));
  }, []);

  const checkEmailBlur = useCallback(() => {
    if (email.current.value.trim().length === 0) {
      alert("請輸入電子信箱");
      return false;
    }
    if (!checkEmail(email.current.value) && email.current.value.length !== 0) {
      alert("電子信箱格式錯誤");
      return false;
    }
    setEmailCheck(true);
    window.Mailcheck.run({
      email: email.current.value,
      domains: domains,
      topLevelDomains: topLevelDomains,
      suggested: function (suggestion) {
        setSuggestion(suggestion.full);
      },
    });
    return true;
  }, []);

  const suggest = useCallback(() => {
    email.current.value = suggestionText;
    setSuggestion("");
  }, [suggestionText]);

  const checkPhoneBlur = useCallback(() => {
    if (phone.current.value.trim().length === 0) {
      alert("請輸入手機號碼");
      return false;
    }
    if (!checkPhone(phone.current.value) && phone.current.value.length !== 0) {
      alert("手機號碼格式不正確，請輸入格式 0912345678");
      return false;
    }
    setPhoneCheck(true);
    return true;
  }, []);

  const finalSent = (e) => {
    e.preventDefault();
    if (isSent.current) return;
    if (!checkEmailBlur()) return;
    if (!checkPhoneBlur()) return;

    //------ 團體 ------
    if (!selected.isSingle && team.current.value === "請選擇") {
      alert("請選擇團體類型");
      return;
    }

    //------ 個人 ------
    if (lastName.current.value.trim().length === 0) {
      alert("請輸入姓氏欄位");
      return;
    }
    if (firstName.current.value.trim().length === 0) {
      alert("請輸入名字欄位");
      return;
    }
    // if (!emailCheck) {
    //   if (email.current.value.trim().length === 0) {
    //     alert("請輸入電子信箱");
    //     return;
    //   } else {
    //     alert("Email 格式錯誤");
    //     return;
    //   }
    // }
    // if (!phoneCheck) {
    //   if (phone.current.value.trim().length === 0) {
    //     alert("請輸入電話");
    //     return;
    //   } else {
    //     alert("電話格式不正確，請輸入格式 0912345678 ");
    //     return;
    //   }
    // }
    if (selected.isSingle && birthdate.current.value === "請選擇") {
      alert("請選擇出生年");
      return;
    }

    //---- 團體flag ----
    if (!selected.isSingle && flagRadio === null) {
      alert("請勾選是否有意將活動旗幟掛放在店內／校內／組織辦公室內");
      return;
    }

    if (!privacy.current.checked) {
      alert("請勾選已閱讀並且確認保密協定內容");
      return;
    }

    const list = {
      Email: email.current.value,
      LastName: lastName.current.value,
      FirstName: firstName.current.value,
      MobilePhone: phone.current.value,
      Birthdate: selected.isSingle ? birthdate.current.value : "1900-01-01",
      CampaignData1__c: selected.isSingle ? "個人" : team.current.value,
      CampaignData2__c: selected.isSingle ? challengeSelected : flagRadio,
      OptIn: true,
      MobileCountryCode: 886,
      req: "post_data",
      LeadSource: "Petition - Health",
      Petition_Interested_In_Arctic__c: false,
      Petition_Interested_In_Climate__c: false,
      Petition_Interested_In_Forest__c: false,
      Petition_Interested_In_Health__c: true,
      Petition_Interested_In_Oceans__c: false,
      Petition_Interested_In_Plastics__c: false,
      CampaignId: "7012u000000P9TFAA0",
      UtmMedium: "",
      UtmSource: "",
      UtmCampaign: "",
      UtmContent: "",
      UtmTerm: "",
      DonationPageUrl: "",
    };

    const formData = new FormData();
    Object.keys(list).forEach((key) => {
      formData.append(key, list[key]);
    });

    isSent.current = true;
    sentBtn.current.style.cursor = "no-drop";

    Swal.fire({
      title: "資料送出中",
      showConfirmButton: false,
      icon: "info",
    }).then(() => {
      // empty
      email.current.value = "";
      lastName.current.value = "";
      firstName.current.value = "";
      phone.current.value = "";
      if (selected.isSingle) birthdate.current.value = "請選擇";
      if (!selected.isSingle) team.current.value = "請選擇";
      if (!selected.isSingle) {
        document.getElementById("y").checked = false;
        document.getElementById("n").checked = false;
      }
      setFlagRadio(null);
      setSuggestion("");
      privacy.current.checked = false;
      isSent.current = false;
      sentBtn.current.style.cursor = "pointer";
    });

    axios({
      method: "POST",
      url: "https://cors-anywhere.small-service.gpeastasia.org/https://cloud.greentw.greenpeace.org/websign",
      data: formData,
    }).then((response) => {
      let resp = response.status;
      switch (resp) {
        case 200:
        case 201:
          sendPetitionTracking("2021-meatless_day");
          Swal.update({
            title: "報名成功",
            text: `您報名的場次為${
              selected.isSingle ? challengeSelected : team.current.value
            }`,
            showConfirmButton: true,
            icon: "success",
            confirmButtonText: "關閉",
          });

          break;
        default:
          isSent.current = false;
          sentBtn.current.style.cursor = "pointer";
          alert("發生錯誤。" + response.status);
      }
    });
  };

  return (
    <FlowContainer id="flowForm">
      <Box>
        <ForSingle>
          <Mirroring src={mirroring} alt="left-hand" />
          <Session>
            <p>您要報名的場次為：</p>
            <div>{challengeSelected}</div>
          </Session>
          <Mirroring src={mirroring} alt="left-hand" />
        </ForSingle>
        <FormBox>
          <form autoComplete="off">
            <p>請於下方填寫素立報名表：</p>
            {selected.isSingle === false && (
              <ListMultiple>
                <FormItems>
                  <p>團體類型</p>
                  <select name="CampaignData1__c" ref={team}>
                    <option value="請選擇">請選擇</option>
                    <option value="店家">店家</option>
                    <option value="餐廳">餐廳</option>
                    <option value="大專院校">大專院校</option>
                  </select>
                </FormItems>
                <FormItems>
                  <p style={{ fontSize: 20 }}>報名後會由專案人員向您聯繫</p>
                </FormItems>
              </ListMultiple>
            )}
            <ListMultiple>
              <FormItems>
                <p>姓</p>
                <input
                  id="LastName"
                  ref={lastName}
                  name="LastName"
                  type="text"
                  placeholder="* 姓氏"
                  required
                />
              </FormItems>
              <FormItems>
                <p>名</p>
                <input
                  id="FirstName"
                  ref={firstName}
                  name="FirstName"
                  type="text"
                  placeholder="* 名字"
                  required
                />
              </FormItems>
            </ListMultiple>
            <ListSingle>
              <FormItems>
                <p>電子信箱</p>
                <input
                  ref={email}
                  id="Email"
                  name="Email"
                  type="email"
                  placeholder="* 電子信箱"
                  onChange={() => setEmailCheck(false)}
                  onBlur={checkEmailBlur}
                />
                {suggestionText !== "" && (
                  <Suggestion onClick={suggest}>
                    您想輸入的是 {suggestionText} 嗎?
                  </Suggestion>
                )}
              </FormItems>
            </ListSingle>
            <ListMultiple>
              <FormItems>
                <p>手機號碼</p>
                <input
                  ref={phone}
                  onBlur={checkPhoneBlur}
                  onChange={() => setPhoneCheck(false)}
                  id="MobilePhone"
                  name="MobilePhone"
                  maxLength="10"
                  required
                  type="text"
                  placeholder="* 0912345678"
                />
              </FormItems>
              <FormItems>
                {selected.isSingle !== false && (
                  <>
                    <p>出生年</p>
                    <select ref={birthdate} id="Birthdate" name="Birthdate">
                      <option value="請選擇">請選擇</option>
                      {optionsItemArr}
                    </select>
                  </>
                )}
              </FormItems>
            </ListMultiple>
            {selected.isSingle === false && (
              <Flag>
                <div>
                  <Ask>
                    是否有意將活動旗幟掛
                    <br />
                    放在店內／校內／組織辦公室內
                  </Ask>
                  <RadioBox>
                    <CheckEl htmlFor="y">
                      <input
                        type="radio"
                        name="flag"
                        id="y"
                        onChange={() => setFlagRadio("y")}
                      />
                      <div />
                      <p>是</p>
                    </CheckEl>
                    <CheckEl htmlFor="n">
                      <input
                        type="radio"
                        name="flag"
                        id="n"
                        onChange={() => setFlagRadio("n")}
                      />
                      <div />
                      <p>否</p>
                    </CheckEl>
                  </RadioBox>
                </div>
                <div>
                  <img src={flagImg} alt="" />
                </div>
              </Flag>
            )}

            <ListSingle>
              <CheckElB htmlFor="secret">
                <input
                  type="checkbox"
                  id="secret"
                  ref={privacy}
                  defaultChecked
                />
                <div />
                <p>
                  我願意收到綠色和平發送的電子通訊，掌握環保工作的最新消息！我並同意綠色和平按照其
                  <a
                    href="https://www.greenpeace.org/taiwan/policies/privacy-and-cookies/?ref="
                    target="_blank"
                    rel="noreferrer"
                    style={{ color: "#646464", textDecoration: "underline" }}
                  >
                    個人隱私政策
                  </a>
                  與我保持聯絡。
                </p>
              </CheckElB>
            </ListSingle>
            <Btn type="submit" onClick={finalSent} ref={sentBtn}>
              確認送出
            </Btn>
          </form>
          <img
            src={grass}
            style={{ position: "absolute", bottom: 0, left: 0, width: "30%" }}
            alt="grass"
          />
          <FireLeft>
            <img src={fireL} alt="fire-left" />
          </FireLeft>
          <FireRight>
            <img src={fireR} alt="fire-right" />
          </FireRight>
        </FormBox>
        <img
          src={corn}
          style={{
            position: "absolute",
            bottom: "-8%",
            right: "-7%",
            width: "35%",
          }}
          alt="corn"
        />
      </Box>
    </FlowContainer>
  );
};

export default Form;
