import React from "react";
import styled from "@emotion/styled";
import FlowContainer from "./FlowContainer";
import illustrate from "../../images/illustrate.png";
import one from "../../images/step1.png";
import two from "../../images/step2.png";
import three from "../../images/step3.png";
import step1 from "../../images/step-1.svg";
import step2 from "../../images/step-2.svg";
import step3 from "../../images/step-3.svg";

const StepContainer = styled.div`
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  flex-wrap: wrap;
  margin: 100px auto;
  @media screen and (max-width: 900px) {
    justify-content: center;
  }
`;

const StepEgg = styled.div`
  width: 31%;
  position: relative;
  background-color: #f3f6e7;
  /* padding: 40px 20px; */
  border-radius: 50%;
  &::after {
    content: "";
    padding-bottom: 100%;
    display: block;
  }
  @media screen and (max-width: 900px) {
    width: 60%;
    &:not(:last-of-type) {
      margin-bottom: 150px;
    }
  }
  @media screen and (max-width: 767px) {
    width: 100%;
  }
`;

const StepImg = styled.img`
  width: auto;
  height: 30%;
  position: absolute;
  top: -40px;
  left: 0;
`;

const Info = styled.div`
  width: 75%;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
`;

const P = styled.p`
  font-weight: 500;
  font-size: 18px;
  line-height: 32px;
  text-align: center;
  color: #36563c;
  @media screen and (max-width: 1200px) {
    font-size: 18px;
    line-height: 28px;
  }
  @media screen and (max-width: 1100px) {
    font-size: 14px;
    line-height: 24px;
  }
  @media screen and (max-width: 900px) {
    /* font-size: 18px; */
  }
`;

const BottomP = styled(P)`
  margin-top: 20px;
  color: #000;
  span {
    display: block;
    font-size: 32px;
    @media screen and (max-width: 1200px) {
      font-size: 26px;
    }
  }
`;

const StepIcon = styled.img`
  position: absolute;
  bottom: -50%;
  left: 50%;
  transform: translate(-50%, -50%);
  @media screen and (max-width: 1024px) {
    bottom: -60%;
  }
  @media screen and (max-width: 1000px) {
    display: none;
  }
  @media screen and (max-width: 767px) {
    bottom: -45%;
    display: block;
  }
`;

const Arrow = styled.div`
  position: absolute;
  top: -50px;
  left: ${(props) => (props.left === true ? 29 : 69)}%;
  border-bottom: 50px solid white;
  border-left: 30px solid transparent;
  border-right: 30px solid transparent;
  transform: translateX(-50%);
  transition: all 0.5s;
  @media screen and (max-width: 767px) {
    left: ${(props) => (props.left === true ? 24 : 77)}%;
  }
`;

const TeamEgg = styled.div`
  width: 100%;
  max-width: 800px;
  position: relative;
  background-color: #f3f6e7;
  border-radius: 50%;
  &::after {
    content: "";
    padding-bottom: 50%;
    display: block;
  }
  img {
    &:first-of-type {
      width: auto;
      height: 54%;
      position: absolute;
      top: -80px;
      left: 0;
    }
    &:last-of-type {
      position: absolute;
      bottom: 0;
      right: 0;
      width: 25%;
    }
  }
  @media screen and (max-width: 767px) {
    &::after {
      padding-bottom: 110%;
    }
    img {
      &:first-of-type {
        height: 35%;
        top: -50px;
      }
    }
  }
`;

const TeamBtn = styled.div`
  cursor: pointer;
  width: 90%;
  max-width: 260px;
  margin: 40px auto 0;
  border-radius: 40px;
  background-color: #36563c;
  padding: 16px 10px;
  text-align: center;
  p {
    position: relative;
    display: inline-block;
    color: #fff;
    font-size: 24px;
    letter-spacing: 0.1em;
    font-weight: bold;
    &::after {
      content: "!";
      display: inline-block;
      transform: rotate(15deg);
      font-size: 26px;
      position: absolute;
      top: -2px;
      right: -18px;
    }
  }
  @media screen and (max-width: 1336px) {
    padding: 10px;
    p {
      font-size: 20px;
      &::after {
        top: -6px;
      }
    }
  }
  @media screen and (max-width: 767px) {
    max-width: 160px;
    p {
      font-size: 18px;
    }
  }
`;

const Step = ({ isSingle, handleSelected }) => {
  return (
    <FlowContainer title={"活動流程"}>
      {isSingle !== null && <Arrow left={isSingle ? true : false} />}
      <StepContainer style={{ justifyContent: isSingle === false && "center" }}>
        {isSingle !== false ? (
          <>
            <StepEgg>
              <StepImg src={one} />
              <Info>
                <P>
                  請於下方選擇想要挑戰等級，
                  <br />
                  並依照欄位內容填寫完畢後送出，即完成報名步驟。
                </P>
                <BottomP>
                  活動報名日期：<span>即日起 – 12/15止</span>
                </BottomP>
              </Info>
              <StepIcon src={step1} style={{ width: "55%" }} alt="step-1" />
            </StepEgg>
            <StepEgg>
              <StepImg src={two} />
              <Info>
                <P>
                  報名完成後，依照所選擇之方案即刻自主開始無肉挑戰 !
                  <br />
                  在12/31前於個人社群 ( FB 或是 Instagram)
                  公開以貼文形式分享無肉照片
                  <br />
                  並加上指定Hashtag
                  <br /> #1125全體素立 <br />
                  #MeatlessDayRocksOn
                </P>
              </Info>
              <StepIcon src={step2} style={{ width: "40%" }} alt="step-2" />
            </StepEgg>
            <StepEgg>
              <StepImg src={three} />
              <Info>
                <P>
                  完成報名者即可獲得參加獎
                  <br />
                  【愛地球勳章1枚】
                  <br />
                  完成挑戰照片分享者即可獲得愛地球勳章一枚並同時符合抽好禮資格
                </P>
              </Info>
              <StepIcon src={step3} style={{ width: "45%" }} alt="step-3" />
            </StepEgg>
          </>
        ) : (
          <TeamEgg>
            <img src={illustrate} alt="illustrate" />
            <Info>
              <P>
                點選下方【我要報名】按鈕後，
                <br />
                請依照類別與表單欄位填寫，
                <br />
                填寫完畢將會由專人與您聯繫，敬請耐心等候。
              </P>
              <TeamBtn
                onClick={() => {
                  handleSelected((prev) => ({
                    ...prev,
                    challengeSelected: "團體素立",
                  }));
                }}
              >
                <p>我要報名</p>
              </TeamBtn>
            </Info>
            <img src={step3} alt="illustrate" />
          </TeamEgg>
        )}
      </StepContainer>
    </FlowContainer>
  );
};

export default Step;
