import React from "react";
import styled from "@emotion/styled";
import FlowContainer from "./FlowContainer";
import { nanoid } from "nanoid";
import hand from "../../images/little-hand.svg";
import level1 from "../../images/level-1.svg";
import level2 from "../../images/level-2.svg";
import level3 from "../../images/level-3.svg";

const SubTitle = styled.p`
  font-weight: 500;
  font-size: 22px;
  letter-spacing: 0.1em;
  margin-top: 20px;
  line-height: 36px;
  text-align: center;
  color: #36563c;
  span {
    background-color: #36563c;
    color: #fff;
  }
  @media screen and (max-width: 767px) {
    font-size: 18px;
    line-height: 28px;
  }
`;

const LevelContainer = styled.div`
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  flex-wrap: wrap;
  margin-top: 100px;
  @media screen and (max-width: 900px) {
    justify-content: center;
    align-items: center;
    flex-direction: column;
    margin-top: 60px;
  }
`;

const LevelItems = styled.div`
  width: 31%;
  display: flex;
  flex-direction: column;
  @media screen and (max-width: 900px) {
    width: 100%;
    &:not(:last-of-type) {
      margin-bottom: 80px;
    }
  }
`;
const Grade = styled.p`
  font-weight: 500;
  font-size: 30px;
  text-align: center;
  letter-spacing: 0.1em;
  color: #ed783f;
  display: flex;
  margin: auto;
  position: relative;
  &::before {
    content: "";
    width: 100%;
    height: 2px;
    background-color: #ed783f;
    position: absolute;
    top: -4px;
    left: 0;
  }
  &::after {
    content: "";
    width: 100%;
    height: 2px;
    background-color: #ed783f;
    position: absolute;
    bottom: -4px;
    left: 0;
  }
  @media screen and (max-width: 767px) {
    font-size: 24px;
  }
`;
const Day = styled.p`
  font-weight: bold;
  margin-top: 25px;
  font-size: 40px;
  text-align: center;
  letter-spacing: 0.1em;
  color: #231916;
  @media screen and (max-width: 767px) {
    font-size: 32px;
    margin-top: 15px;
  }
`;

const FingerArea = styled.div`
  width: 100%;
  max-width: ${(props) => props.width}px;
  margin: 5px auto 0;
  height: 70px;
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  img {
    width: ${(props) => props.imgWidth}%;
    height: auto;
    display: block;
    margin: 0px 2px;
  }
`;
const P = styled.p`
  margin-top: 20px;
  font-weight: 500;
  font-size: 20px;
  line-height: 28px;
  text-align: center;
  color: #000000;
  @media screen and (max-width: 767px) {
    margin-top: 15px;
    font-size: 18px;
  }
`;

const LevelIcon = styled.img`
  width: 100%;
  max-width: 290px;
  display: block;
  margin: 30px auto;
`;

const FoodP = styled.p`
  font-weight: 500;
  font-size: 22px;
  text-align: center;
  color: #36563c;
`;

const Level = () => {
  return (
    <FlowContainer title={"超強的獎勵內容"}>
      <SubTitle>
        為了感謝您的參與，完成任務後每人頒發「愛地球勳章一枚」
        <br />
        另外，每個等級抽出 15 位幸運的民眾獲得超強的獎勵！
        <br />
        <span>
          我們將於 2022 年 1 月公佈中獎名單，並透過EMAIL和社群聯繫獲獎者
        </span>
      </SubTitle>
      <LevelContainer>
        <LevelItems>
          <Grade>等級1</Grade>
          <Day>03天</Day>
          <FingerArea width={184} imgWidth={23}>
            {Array.from({ length: 3 }).map((id = nanoid()) => (
              <img src={hand} alt="little-hand" key={id} />
            ))}
          </FingerArea>
          <P>
            抽出 15 位
            <br />
            輕鬆過關的民眾
          </P>
          <LevelIcon src={level1} alt="level-1" />
          <FoodP>植物奶 1 瓶</FoodP>
        </LevelItems>
        <LevelItems>
          <Grade>等級2</Grade>
          <Day>07天</Day>
          <FingerArea width={318} imgWidth={12}>
            {Array.from({ length: 7 }).map((id = nanoid()) => (
              <img src={hand} alt="little-hand" key={id} />
            ))}
          </FingerArea>
          <P>
            抽出 15 位
            <br />
            禁得起誘惑的地球人
          </P>
          <LevelIcon src={level2} alt="level-2" />
          <FoodP>植物奶 1 瓶 + 鷹嘴豆泥 1 罐</FoodP>
        </LevelItems>
        <LevelItems>
          <Grade>等級3</Grade>
          <Day>14天</Day>
          <FingerArea width={237} imgWidth={12}>
            {Array.from({ length: 14 }).map((id = nanoid()) => (
              <img src={hand} alt="little-hand" key={id} />
            ))}
          </FingerArea>
          <P>
            抽出 15 位
            <br />
            捨己救地球的的英雄
          </P>
          <LevelIcon src={level3} alt="level-3" />
          <FoodP>植物奶 1 瓶 + 鷹嘴豆泥 1 罐 + 小農果醬 1 瓶</FoodP>
        </LevelItems>
      </LevelContainer>
    </FlowContainer>
  );
};

export default Level;
